<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Kategori Soal</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addKS()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <!-- <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="select"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(skd, idx) in fSKD"
                  v-bind:key="idx"
                  :value="skd.slug"
                >
                  {{ skd.fileName }}
                </option>
              </TailSelect>
            </div> -->
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataKS.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                  v-html="k"
                >
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataKS.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <span class="uppercase">{{ u.kode }}</span> / {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.nilaiPg }} / {{ u.jmlSoal }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap  uppercase">
                  {{ u.jenisSoal.kode }} / {{ u.jenisSoal.paket }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.isActive ? "Active" : "Suspend" }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.createdAt }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailKSApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editKS(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteKSApi(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataKS.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Kategori Soal.</p>
          </div>
          <LoadingDiv id="loadingKSadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>
    <!-- Modal Add and Edit Kategori Soal -->
    <div id="modal-data-ks" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Kategori Soal" : "Edit Kategori Soal" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-ks')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Kode"
              v-model="exDataKS.kode"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title"
              v-model="exDataKS.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nilai PassingGrade</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Nilai PassingGrade"
              v-model="exDataKS.nilaiPg"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jumlah Soal</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Jumlah Soal"
              v-model="exDataKS.jmlSoal"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-full sm:w-1/2">
              <TailSelect
                v-model="exDataKS.isActive"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(ss, idx) in listStatus"
                  v-bind:key="idx"
                  :value="ss.value"
                >
                  {{ ss.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div
            class="flex flex-col sm:flex-row items-center mt-3"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jenis Soal</label>
            <div class="w-full sm:w-1/2">
              <TailSelect
                v-model="exDataKS.idJenis"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(ss, idx) in listKodeKS"
                  v-bind:key="idx"
                  :value="ss.value"
                  @click="exDataKS.idJenis = ss.id"
                >
                  {{ ss.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-ks')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addKSApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editKSApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Kategori Soal -->
    <div id="delete-modal-data-ks" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteKS.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-ks')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="fixDeleteKS();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail Kategori Soal -->
    <div id="modal-detail-ks" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Kategori Soal</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-ks')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode</label>
            <p class="text-gray-500">{{ detailKS.kode }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <p class="text-gray-500">{{ detailKS.title }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nilai PassingGrade</label>
            <p class="text-gray-500">{{ detailKS.nilaiPg }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jumlah Soal</label>
            <p class="text-gray-500">{{ detailKS.jmlSoal }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <p class="text-gray-500">{{ detailKS.isActive ? "Active" : "Suspend" }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataStatus } from "./kategori-soal";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = dataStatus();

    const titleTable: Array<string> = [
      "Kode / Title",
      "Nilai PassingGrade / <br> Jumlah Soal",
      "Jenis Soal",
      "Status",
      "Created",
      "#",
    ];

    const dataKS = reactive({
      data: [] as any,
      search: "",
    });

    const getData = async () => {
      cash("#loadingKSadmin").show();

      const search = dataKS.search;
      axios
        .get(
          api.adminKategoriSoal,
          configGet
        )
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log("response get js ", rs);

          dataKS.data = rs;
          // console.log("dataKS.paginate ", dataKS.paginate);
          cash("#loadingKSadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingKSadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataKS,
      configGet,
      configXwfu,
      listStatus,
    };
  },
  data() {
    return {
      // for modal data user
      section: "add",
      msgSuccess: "",

      detailKS: {
        id: 0,
        kode: "",
        title: "",
        nilaiPg: "",
        jmlSoal: "",
        isActive: "1",
        idJenis: "0",
      } as any,

      exDataKS: {
        id: 0,
        kode: "",
        title: "",
        nilaiPg: "",
        jmlSoal: "",
        isActive: "1",
        idJenis: "0",
      },

      exDeleteKS: {
        id: 0,
        title: "",
      },

      listKodeKS: [],
    };
  },
  methods: {
    async listJenisSoalApi(callback: any): Promise<void> {
      gpf.gLoading.show();
      axios
        .get(
          api.adminJenisSoal,
          this.configGet
        )
        .then(async (result: any) => {
          gpf.gLoading.hide();
          const rs = result.data.response;
          // console.log("response get js ", rs);
          this.exDataKS.idJenis = rs[0].id;
          this.listKodeKS = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await callback();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async emptyField() {
      const ex = this.exDataKS;
      ex.kode = "";
      ex.title = "";
      ex.nilaiPg = "";
      ex.jmlSoal = "";
      ex.isActive = "1";
      ex.idJenis = "";
    },
    async addKS() {
      await this.emptyField();
      this.section = "add";
      const showMKS = () =>{ gpf.showModal("#modal-data-ks") };
      this.listJenisSoalApi( showMKS );
    },
    addKSApi() {
      gpf.gLoading.show();
      const ex = this.exDataKS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("title", ex.title);
      params.append("nilaiPg", ex.nilaiPg);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("isActive", ex.isActive);
      params.append("idJenis", ex.idJenis);

      axios
        .post(api.adminKategoriSoal, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-ks");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async editKS(data: any) {
      this.section = "edit";
      const showMKS = () =>{ gpf.showModal("#modal-data-ks") };
      await this.listJenisSoalApi( showMKS );

      gpf.gLoading.show();

      axios
        .get(api.adminKategoriSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.exDataKS;
          ex.id = rs.id;
          ex.kode = rs.kode;
          ex.title = rs.title;
          ex.nilaiPg = rs.nilaiPg;
          ex.jmlSoal = rs.jmlSoal;
          ex.isActive = rs.isActive ? "1" : "0";
          ex.idJenis = rs.idJenis.toString();

          await gpf.showModal("#modal-data-ks");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editKSApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataKS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("title", ex.title);
      params.append("nilaiPg", ex.nilaiPg);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("isActive", ex.isActive);
      params.append("idJenis", ex.idJenis);

      axios
        .put(api.adminKategoriSoal + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-ks");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailKSApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminKategoriSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          this.detailKS = rs;
          await gpf.showModal("#modal-detail-ks");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    showPass(id: string) {
      const idHtml = cash("#"+ id);
      if (idHtml.attr("type") === "password") {
        idHtml.attr("type", "text");
      } else {
        idHtml.attr("type", "password");
      }
    },
    deleteKSApi(data: any): void {
      const ex = this.exDeleteKS;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-ks");
    },
    fixDeleteKS() {
      const ex = this.exDeleteKS;

      gpf.gLoading.show();

      axios
        .delete(api.adminKategoriSoal + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.title = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-ks");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    }
  },
});
</script>
