<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Pengeluaran</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addExp()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <Litepicker
                v-model="dataExpense.dateRange"
                :options="{
                  autoApply: false,
                  singleMode: false,
                  numberOfColumns: 2,
                  numberOfMonths: 2,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input w-56 border block mx-auto"
              />
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-auto mt-2 md:ml-2 md:mt-auto mb-1"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataExpense.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                  v-html="k"
                ></th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataExpense.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 min-w-40">
                  {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 min-w-44">
                  {{ u.description }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.qty }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.convertToCurrency(u.price, "IDR ") }} / <br />
                  {{ $gpf.convertToCurrency(u.total, "IDR ") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.date, "DD - MMM - YYYY HH:mm:ss") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.createdAt, "DD - MMM - YYYY HH:mm:ss") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.createdBy }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailExpApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editExp(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteExp(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataExpense.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Pengeluaran.</p>
          </div>
          <LoadingDiv id="loadingDPadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataExpense.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataExpense.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataExpense.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataExpense.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataExpense.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataExpense.page !== dataExpense.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataExpense.page !== dataExpense.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataExpense.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
    <!-- Modal Add and Edit Pengeluaran -->
    <div id="modal-data-exp" class="modal p-0">
      <div class="modal__content modal__content--xl">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Pengeluaran" : "Edit Pengeluaran" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-exp')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Date</label>
            <div class="relative w-5/6">
              <div
                class="absolute rounded-l w-10 h-full flex items-center justify-center bg-gray-100 border text-gray-600 dark:bg-dark-1 dark:border-dark-4"
              >
                <CalendarIcon class="w-4 h-4" />
              </div>
              <Litepicker
                v-model="exDataExp.date"
                :options="{
                  autoApply: false,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input pl-12 border text-center"
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title"
              v-model="exDataExp.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5"
              >Description</label
            >
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Description"
              v-model="exDataExp.description"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Qty</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Qty"
              v-model="exDataExp.qty"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Price</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="url link img pertanyaan"
              v-on:keyup="ctc('price')"
              v-model="exDataExp.price"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-exp')"
          >
            Close
          </button>
          <button
            v-if="section === 'add'"
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addExpApi()"
          >
            Simpan
          </button>
          <button
            v-else
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editExpApi()"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail Pengeluaran -->
    <div id="modal-detail-exp" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Pengeluaran</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-exp')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Title</label>
            <p class="text-gray-500">
              {{ detailExp.title }}
            </p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5"
              >Description</label
            >
            <p class="text-gray-500">
              {{ detailExp.description }}
            </p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Qty</label>
            <p class="text-gray-500">
              {{ detailExp.qty }}
            </p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Price</label>
            <p class="text-gray-500">
              {{ detailExp.price }}
            </p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Total</label>
            <p class="text-gray-500">
              {{ detailExp.total }}
            </p>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal Delete Pengeluaran -->
    <div id="delete-modal-data-exp" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteExp.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-exp')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteExpApi()"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataStatus, dataPayType, dataPayStatus } from "./pengeluaran";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import { helper as $h } from "@/utils/helper";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = dataStatus();
    const listPayType = dataPayType();
    const listPayStatus = dataPayStatus();

    // "id": 303,
    // "date": "2021-03-13",
    // "title": "PB Fee",
    // "description": "10 Item",
    // "qty": 1,
    // "price": 30000,
    // "total": 30000,
    // "createdAt": "2021-03-13 09:31:53",
    // "createdBy": "Admin"

    const titleTable: Array<string> = [
      "Title",
      "Description",
      "Qty",
      "Price / <br> Total",
      "Date",
      "Created At",
      "Created By",
      "#",
    ];

    const dataExpense = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataExpense.dateRange = `${$h.formatDate(
      firstDay.toString(),
      "DD MMM, YYYY"
    )} - ${$h.formatDate(lastDay.toString(), "DD MMM, YYYY")}`;

    const getData = async () => {
      cash("#loadingDPadmin").show();

      // console.log("dateRange ", dataExpense.dateRange);
      // console.log("firstDay lastDay ", firstDay, lastDay);

      const page = dataExpense.page;
      const pageSize = dataExpense.pageSize;
      const search = dataExpense.search;
      const since = $h.formatDate(
        dataExpense.dateRange.split("-")[0],
        "YYYY-MM-DD"
      );
      const until = $h.formatDate(
        dataExpense.dateRange.split("-")[1],
        "YYYY-MM-DD"
      );
      axios
        .get(
          api.adminPengeluaran +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize +
            "&since=" +
            since +
            "&until=" +
            until,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataExpense.data = rs;
          dataExpense.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataExpense.paginate ", dataExpense.paginate);
          cash("#loadingDPadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDPadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataExpense.page = p;
      getData();
    };

    const nextPage = () => {
      dataExpense.page = dataExpense.page + 1;
      getData();
    };

    const beforePage = () => {
      dataExpense.page = dataExpense.page - 1;
      getData();
    };

    const lastPage = () => {
      dataExpense.page = dataExpense.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataExpense,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
      listPayType,
      listPayStatus,
    };
  },
  data() {
    return {
      // for modal data bs
      section: "add",
      msgSuccess: "",

      detailExp: {
        id: "",
        date: "2021-03-15",
        title: "Peralatan PLD",
        description: "",
        qty: "0",
        price: "0",
        total: "0",
      },

      exDataExp: {
        id: "",
        date: "2021-03-15",
        title: "Peralatan PLD",
        description: "",
        qty: "0",
        price: "0",
        total: "0",
      },

      exDeleteExp: {
        id: "",
        title: "",
      },
    };
  },
  methods: {
    async emptyField() {
      const dateNow = new Date();
      const ex = this.exDataExp;
      ex.id = "";
      ex.date = $h.formatDate(dateNow.toString(), "DD MMM, YYYY");
      ex.title = "";
      ex.description = "";
      ex.qty = "0";
      ex.price = "0";
      ex.total = "0";
    },
    ctc(f: string) {
      const ex: any = this.exDataExp;
      ex[f] = gpf.ctc(ex[f]);
    },
    addExp() {
      this.section = "add";
      this.emptyField();
      gpf.showModal("#modal-data-exp");
    },
    addExpApi() {
      gpf.gLoading.show();
      const ex = this.exDataExp;
      const params = new URLSearchParams();
      params.append("date", $h.formatDate(ex.date, "YYYY-MM-DD"));
      params.append("title", ex.title);
      params.append("description", ex.description);
      params.append("qty", ex.qty);
      params.append("price", gpf.btc(ex.price));

      axios
        .post(api.adminPengeluaran, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await this.emptyField();
          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-exp");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async editExp(data: any) {
      gpf.gLoading.show();
      setTimeout(() => {
        gpf.gLoading.hide();
        gpf.showModal("#modal-data-exp");
      }, 2000);
      this.section = "edit";
      const ex = this.exDataExp;
      ex.id = data.id;
      ex.date = $h.formatDate(data.date.toString(), "DD MMM, YYYY");
      ex.title = data.title;
      ex.description = data.description;
      ex.qty = data.qty;
      ex.price = data.price;
      ex.total = data.total;

    },
    editExpApi() {
      gpf.gLoading.show();
      const ex = this.exDataExp;
      const params = new URLSearchParams();
      params.append("date", $h.formatDate(ex.date, "YYYY-MM-DD"));
      params.append("title", ex.title);
      params.append("description", ex.description);
      params.append("qty", ex.qty);
      params.append("price", gpf.btc(ex.price));

      axios
        .put(api.adminPengeluaran + '?id=' + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await this.emptyField();
          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-exp");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async detailExpApi(data: any) {
      gpf.gLoading.show();
      setTimeout(() => {
        gpf.gLoading.hide();
      }, 2000);
      this.section = "edit";
      const ex = this.detailExp;
      ex.id = data.id;
      ex.date = $h.formatDate(data.date.toString(), "DD MMM, YYYY");
      ex.title = data.title;
      ex.description = data.description;
      ex.qty = data.qty;
      ex.price = gpf.convertToCurrency(data.price, "IDR ");
      ex.total = gpf.convertToCurrency(data.total, "IDR ");

      await gpf.showModal("#modal-detail-exp");
    },
    deleteExp(data: any): void {
      const ex = this.exDeleteExp;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-exp");
    },
    deleteExpApi() {
      const ex = this.exDeleteExp;

      gpf.gLoading.show();

      axios
        .delete(api.adminPengeluaran + '?id=' + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = "";
          ex.title = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-exp");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
  },
});
</script>
