
import { defineComponent, reactive } from 'vue';
import { useRoute } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const route = useRoute();
    const slug: any = route.params.slug;
    const routeName = route.name;
    const type = routeName === 'member-detail-tips-and-trik' ? 'video' : 'pdf';
    const detail = reactive({
      data: {},
      embed: "",
    })

    console.log('route now ', route.name);
    console.log('type and slug ', type , slug);

    const config = api.configGet();
    axios
      .get(api.memberMateri + `${type}/${slug}`, config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response detail mp ', rs);
        detail.data = rs;
      })
      .catch(async (error: any) => {
        // console.log("response detail mp err ", error.response);
        await gpf.handleEds(error);
      });

    const getEmbed = (url : string) => {
      return gpf.getParameterByNameWL(url, 'v');
    }

    return {
      slug,
      type,
      detail,
      getEmbed,
    }
  }
})
