<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">
            Data Withdrawal Affiliate
          </h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addAW()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>

    <div class="col-span-12 grid grid-cols-12 gap-3 mt-3 intro-y w-full">
      <div
        v-for="(ds, idx) in listKeyDS"
        :key="idx"
        class="col-span-12 sm:col-span-6 xl:col-span-3 intro-y mb-3"
      >
        <div class="report-box zoom-in">
          <div class="box p-5">
            <div class="flex items-center">
              <p class="text-base font-medium mr-2">
                {{ ds.title }} : <br />
                {{ $gpf.convertToCurrency(dataAW.dataSaldo[ds.value], "IDR ") }}
              </p>
              <div class="ml-auto">
                <component :is="ds.icon" :class="'w-10 h-10 ' + ds.color" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <LoadingDiv id="loadingSaldoAff" />

    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          ></div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataAW.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                  v-html="k"
                ></th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataAW.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.accountName }} <br />
                  {{ u.accountNumber }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.bankName }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.convertToCurrency(u.amount, "IDR ") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.createdAt, "DD MMM, YYYY HH:mm:ss") }}
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataAW.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Withdrawal.</p>
          </div>
          <LoadingDiv id="loadingDMaff" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataAW.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataAW.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataAW.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataAW.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataAW.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataAW.page !== dataAW.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataAW.page !== dataAW.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataAW.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- Modal Add and Edit Sales -->
    <div id="modal-data-aw" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Submit Withdrawal</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-aw')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Pin</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Pin"
              v-model="exDataAW.pin"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Amount</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Amount"
              v-on:keyup="ctc('amount')"
              v-model="exDataAW.amount"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Akun Bank</label
            >
            <div class="w-5/6">
              <TailSelect
                v-model="exDataAW.bank"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listBank"
                  v-bind:key="idx"
                  :value="s.id"
                  @click="exDataAW.bank = s.id"
                >
                  {{
                    `${s.code} - ${s.accountNumber} - ${s.name} - ${s.accountName}`
                  }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-aw')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addAWApi()"
          >
            Simpan
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";
import { dataKeyDS } from "./withdrawal";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listKeyDS = dataKeyDS();

    const titleTable: Array<string> = [
      "Account Name / <br> Account Number",
      "Bank Name",
      "Amount",
      "Status",
      "Created",
    ];

    const dataAW = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      dataSaldo: {
        komisi: 0,
        paidWithdrawal: 0,
        unpaidWithdrawal: 0,
        saldo: 0,
      } as any,
    });

    const dataSaldo = async () => {
      cash("#loadingSaldoAff").show();

      const ds = await api.getSaldo();
      dataAW.dataSaldo = ds;

      cash("#loadingSaldoAff").hide();
    };

    const getData = async () => {
      dataSaldo();
      cash("#loadingDMaff").show();

      const page = dataAW.page;
      const pageSize = dataAW.pageSize;
      const search = dataAW.search;
      const url =
        api.affWithdrawal +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;

      const hide = () => cash("#loadingDMaff").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataAW.data = rs;
      dataAW.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataAW.page = p;
      getData();
    };

    const nextPage = () => {
      dataAW.page = dataAW.page + 1;
      getData();
    };

    const beforePage = () => {
      dataAW.page = dataAW.page - 1;
      getData();
    };

    const lastPage = () => {
      dataAW.page = dataAW.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataAW,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listKeyDS,
    };
  },
  data() {
    return {
      exDataAW: {
        id: "",
        pin: "",
        amount: "",
        bank: "",
      },
      listBank: [],
    };
  },
  methods: {
    async emptyField() {
      const ex = this.exDataAW;
      ex.id = "0";
      ex.pin = "";
      ex.amount = "";
      ex.bank = "";
    },
    ctc(f: string) {
      const ex: any = this.exDataAW;
      ex[f] = gpf.ctc(ex[f]);
    },
    async addAW() {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();

      await this.emptyField();
      const lb = await api.getAkunBank(hide);
      this.listBank = lb;
      this.exDataAW.bank = lb[0].id ? lb[0].id : "";
      console.log("listBank ", this.listBank);
      gpf.showModal("#modal-data-aw");

    },
    async addAWApi() {
      gpf.gLoading.show();
      const ex = this.exDataAW;
      const params = new URLSearchParams();
      params.append("pin", ex.pin);
      params.append("amount", gpf.btc(ex.amount));
      params.append("bank", ex.bank);

      const hitApi = await api.hitApiPost(api.affWithdrawal, params);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#modal-data-aw");
      await this.emptyField();
      await this.getData();

      gpf.gLoading.hide();
    },
  },
});
</script>
