
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataStatus, dataPaket } from "./master-soal";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";
import importExcelFunc from "@/components/import-excel/Main.vue";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = dataStatus();
    const listPaket = dataPaket();

    // "id": 1474,
    // "paket": "cpns",
    // "jenis": "Seleksi Kompetensi Dasar",
    // "kategori": "Tes Wawasan Kebangsaan",
    // "bidang": "Nasionalisme",
    // "question": "Pembentukan konstitusi di Indonesia, menggunakan cara.\r\n",
    // "answer": "C",
    // "createdAt": "2021-02-20 00:00:00"

    const titleTable: Array<string> = [
      "Paket",
      "Jenis Soal",
      "Kategori Soal",
      "Master Soal",
      "Pertanyaan",
      "Kunci Jawaban",
      "Created",
      "#",
    ];

    const dataMS = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      paket: listPaket[0].value,
      kategori: "",
      listKategori: [],
      bidang: "",
      listBidang: [],
    });

    const getDataListMS = async () => {
      cash("#loadingBSadmin").show();

      const page = dataMS.page;
      const pageSize = dataMS.pageSize;
      const search = dataMS.search;
      const kategori = dataMS.kategori;
      const bidang = dataMS.bidang;
      const paket = dataMS.paket;
      axios
        .get(
          api.adminMasterSoal +
            "?search=" +
            search +
            "&paket=" +
            paket +
            "&kategori=" +
            kategori +
            "&bidang=" +
            bidang +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          gpf.gLoading.hide();
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataMS.data = rs;
          dataMS.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataMS.paginate ", dataMS.paginate);
          cash("#loadingBSadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingBSadmin").hide();
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    };

    const getListBidang = async () => {
      axios
        .get(api.adminAllBidangSoal, configGet)
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          dataMS.bidang = rs[0].title;
          dataMS.listBidang = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.title,
              id: e.id,
            };
            return obj;
          });

          await getDataListMS();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    };

    const getListKategori = async () => {
      gpf.gLoading.show();
      axios
        .get(api.adminKategoriSoal, configGet)
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          dataMS.kategori = rs[0].kode;
          dataMS.listKategori = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.kode,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await getListBidang();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    };

    getListKategori();

    const changePS = () => {
      getDataListMS();
    };

    const goToPage = (p: number) => {
      dataMS.page = p;
      getDataListMS();
    };

    const nextPage = () => {
      dataMS.page = dataMS.page + 1;
      getDataListMS();
    };

    const beforePage = () => {
      dataMS.page = dataMS.page - 1;
      getDataListMS();
    };

    const lastPage = () => {
      dataMS.page = dataMS.paginate.totalPages;
      getDataListMS();
    };

    return {
      salesReportFilter,
      titleTable,
      getDataListMS,
      dataMS,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
      listPaket,
      getListKategori,
    };
  },
  components: {
    importExcelFunc,
  },
  data() {
    return {
      // for modal data bs
      section: "add",
      msgSuccess: "",

      detailMS: {
        id: 0,
        idJenis: "",
        idCategory: "",
        idBidang: "",
        question: "",
        questionImg: "",
        pembahasan: "",
        pembahasanImg: "",
        answer: "",
        option: [
          {
            option: "A",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "B",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "C",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "D",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "E",
            text: "",
            img: "",
            poin: 0,
          },
        ] as any,
      } as any,

      exDataMS: {
        id: 0,
        idJenis: "",
        idCategory: "",
        idBidang: "",
        question: "",
        questionImg: "",
        pembahasan: "",
        pembahasanImg: "",
        answer: "",
        option: [
          {
            option: "A",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "B",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "C",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "D",
            text: "",
            img: "",
            poin: 0,
          },
          {
            option: "E",
            text: "",
            img: "",
            poin: 0,
          },
        ] as any,
      },

      optionObj: {
        option: "",
        text: "",
        img: "",
        poin: 0,
      },

      exDeleteMS: {
        id: 0,
        question: "",
      },

      exListMS: {
        jenis: [],
        kategori: [],
        bidang: [],
      },

      listKategoriMS: [],
      listJenisMS: [],
      listBidangMS: [],
      showModalMS: "" as any,
    };
  },
  methods: {
    async listBidangSoalApi(): Promise<void> {
      const showMKS = async () => {
        gpf.showModal("#modal-data-bs");
        setTimeout(() => {
          cash("#modal-data-bs").css({ "padding-left": "0px !important" });
        }, 10000);
      };

      axios
        .get(api.adminAllBidangSoal, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();
          const rs = result.data.response;
          // console.log("response get js ", rs);
          if (this.section === "add") {
            this.exDataMS.idBidang = rs[0].id;
          }
          this.exListMS.bidang = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              id: e.id,
            };
            return obj;
          });

          await showMKS();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async listKategoriSoalApi(): Promise<void> {
      axios
        .get(api.adminKategoriSoal, this.configGet)
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log("response get js ", rs);
          if (this.section === "add") {
            this.exDataMS.idCategory = rs[0].id;
          }
          this.exListMS.kategori = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await this.listBidangSoalApi();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async listJenisSoalApi(): Promise<void> {
      axios
        .get(api.adminJenisSoal, this.configGet)
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log("response get js ", rs);
          if (this.section === "add") {
            this.exDataMS.idJenis = rs[0].id;
          }
          this.exListMS.jenis = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await this.listKategoriSoalApi();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async emptyField() {
      const ex = this.exDataMS;
      ex.idJenis = "";
      ex.idCategory = "";
      ex.idBidang = "";
      ex.question = "";
      ex.questionImg = "";
      ex.pembahasan = "";
      ex.pembahasanImg = "";
      ex.answer = "";
      ex.option = ex.option.map(() => {
        const obj = {
          text: "",
          img: "",
          poin: 0,
        };
        return obj;
      });
    },
    async addMS() {
      await this.emptyField();
      this.section = "add";
      gpf.gLoading.show();
      // gpf.showModal("#modal-data-bs");

      await this.listJenisSoalApi();
    },
    addMSApi() {
      gpf.gLoading.show();
      const ex = this.exDataMS;

      axios
        .post(api.adminMasterSoal, ex, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-bs");
          await this.emptyField();
          await this.getDataListMS();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editMS(data: any) {
      this.section = "edit";
      gpf.gLoading.show();

      axios
        .get(api.adminMasterSoal + data.id, this.configGet)
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log("response get deu ", rs);
          const ex = this.exDataMS;
          ex.id = rs.id;
          ex.idJenis = rs.idJenis.toString();
          ex.idCategory = rs.idKategori.toString();
          ex.idBidang = rs.idBidang.toString();
          ex.question = rs.question;
          ex.questionImg = rs.questionImg;
          ex.pembahasan = rs.pembahasan;
          ex.pembahasanImg = rs.pembahasanImg;
          ex.answer = rs.answer;
          ex.option = rs.options;

          await this.listJenisSoalApi();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editMSApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataMS;

      axios
        .put(api.adminMasterSoal + ex.id, ex, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-bs");
          await this.emptyField();
          await this.getDataListMS();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailMSApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminMasterSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          this.detailMS = rs;

          await gpf.showModal("#modal-detail-bs");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    deleteMS(data: any): void {
      const ex = this.exDeleteMS;
      ex.id = data.id;
      ex.question = data.question;

      gpf.showModal("#delete-modal-data-ms");
    },
    deleteMSApi() {
      const ex = this.exDeleteMS;

      gpf.gLoading.show();

      axios
        .delete(api.adminMasterSoal + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.question = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-ms");
          await this.getDataListMS();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async importExcelFunc(data?: any) {
      console.log("importExcelFunc ", data);

      typeof data === "string"
        ? gpf.showModal("#modal-import-excel")
        : await this.uploadImportExcel(data);
    },
    convertBodyIE(data: any) {
      const result = data.map((e: any) => {
        const obj = {
          jenis: e["JENIS"],
          kategori: e["KATEGORI"],
          bidang: e["BIDANG"],
          pertanyaan: e["PERTANYAAN"],
          questionImg: e["GAMBAR PERTANYAAN"],
          jawaban: e["JAWABAN"],
          pembahasan: e["PEMBAHASAN"],
          pembahasanImg: e["GAMBAR PEMBAHASAN"],
          options: [
            {
              opsi: "A",
              text: e["OPSI A"],
              img: e["GAMBAR A"],
              bobot: e["BOBOT A"],
            },
            {
              opsi: "B",
              text: e["OPSI B"],
              img: e["GAMBAR B"],
              bobot: e["BOBOT B"],
            },
            {
              opsi: "C",
              text: e["OPSI C"],
              img: e["GAMBAR C"],
              bobot: e["BOBOT C"],
            },
            {
              opsi: "D",
              text: e["OPSI D"],
              img: e["GAMBAR D"],
              bobot: e["BOBOT D"],
            },
            {
              opsi: "E",
              text: e["OPSI E"],
              img: e["GAMBAR E"],
              bobot: e["BOBOT E"],
            },
          ],
        };
        return obj;
      });
      return result;
    },
    async uploadImportExcel(data: any) {
      gpf.gLoading.show();
      const body = this.convertBodyIE(data);

      // console.log("body IE ", body);

      axios
        .post(api.adminImportSoal, body, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await this.getDataListMS();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
  },
});
