<template>
  <!-- Modal Import Soal -->
  <div id="modal-import-excel" class="modal p-0">
    <div class="modal__content modal__content--lg">
      <div
        class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
      >
        <h2 class="font-medium text-base mr-auto">Import File Excel</h2>
        <a
          href="javascript:;"
          class="right-0 top-0"
          @click="$gpf.hideModal('#modal-import-excel')"
        >
          <xIcon class="w-6 h-6 text-gray-500" />
        </a>
      </div>
      <div class="px-3 md:px-5 py-3">
				<div
					v-cloak 
					@dragover.prevent
					@drop.prevent="handleFile($event, 'drop')" 
					@click="chooseExcel()"
					class="w-full min-h-48 border-dashed border-2 border-gray-500 rounded flex justify-center items-center cursor-pointer"
				>
					<h2 v-if="allData.length === 0">CLick to Upload File or Drag File over.</h2>
					<div v-else class="flex flex-col items-center">
						<img :src="'/icon-svg/excel-logo.svg'" alt="excel-logo" class="w-20">
						<p class="mt-2 text-sm">{{ fileName }} - {{ allData.length }} total data.</p>
					</div>
				</div>

        <div class="flex flex-col sm:flex-row items-center mt-3 hidden">
          <div class="flex flex-col w-10/12">
            <div class="flex items-center mb-2">
              <input
                type="file"
                class="button w-10 border flex-1 hidden"
                placeholder="Banner"
                id="fileImportExcel"
                accept=".xlsx, .xls"
                v-on:change="handleFile($event, 'select')"
              />
              <button
                @click="chooseExcel()"
                class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
              >
                Choose File
              </button>
            </div>
            <span class="text-sm">Maks Ukuran File Banner = 2MB.</span>
          </div>
        </div>
      </div>
      <div
        class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
      >
        <button
          type="button"
          class="button w-20 bg-theme-6 text-white mr-2"
          @click="$gpf.hideModal('#modal-import-excel')"
        >
          Close
        </button>
        <button
          type="button"
          class="button w-20 bg-theme-1 text-white"
					v-bind:disabled="allData.length === 0"
          @click="uploadExcel()"
        >
          Upload
        </button>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import * as XLSX from 'xlsx';
import { gpf } from "@/utils/global-functions";

export default defineComponent({
	setup() {
		const kuyIE = "kuyIE";
		return {
			kuyIE,
		}
	},
	data() {
		return { 
			arrayBuffer: "" as any,
      file: "" as any,
			allData: [] as any,
			fileName: "",
		}
	},
	methods: {
		chooseExcel() {
      const id = document.getElementById("fileImportExcel");
      id?.click();
    },
		getDataFromExcel(evt : HTMLInputElement) {
			const file: any = evt.files;
			this.fileName = file[0].name;

      const fileReader = new FileReader();
      fileReader.onload = () => {
          this.arrayBuffer = fileReader.result;
          var data = new Uint8Array(this.arrayBuffer);
          var arr = new Array();
          for(var i = 0; i !== data.length; ++i) arr[i] = String.fromCharCode(data[i]);
          var bstr = arr.join("");
          var workbook = XLSX.read(bstr, {type:"binary"});
          var firstSheetName = workbook.SheetNames[0];
          var worksheet = workbook.Sheets[firstSheetName];
					const allData = XLSX.utils.sheet_to_json(worksheet,{raw:true});
					this.allData = allData;

					console.log('allData ', allData);
      }

      fileReader.readAsArrayBuffer(file[0]);
		},
    handleFile(evt: any, cond: string) {

			console.log('handleFile evt ', evt, cond)

			cond === 'select' ? this.getDataFromExcel(evt.target) : this.getDataFromExcel(evt.dataTransfer);
    },
		async uploadExcel() {
			this.$emit('importExcelFunc', this.allData);
			await gpf.hideModal('#modal-import-excel');
		}
	}
})
</script>
