<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex sm:flex-row items-center h-auto sm:h-10">
          <h2 class="text-lg font-medium truncate">Kelas Private</h2>
          <div
            class="ml-auto mt-2 sm:mt-0 flex"
          >
						<button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div class="flex md:flex-row items-center col-span-3 md:col-span-2">
          </div>
          <div class="w-full col-span-3 md:col-span-1">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataKP.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataKP.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 min-w-40">
                  {{ u.topik }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.kategori }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.tanggal, "DD - MMM - YYYY") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-15">
                  <Tippy
                    tag="span"
                    class="button inline-block text-white px-2"
                    content="View Respon"
                    @click="detailKP(u)"
                  >
                    <EyeIcon class="text-primary" />
                  </Tippy>
									<Tippy
										tag="span"
										class="button inline-block text-white px-2"
										content="Hapus"
										@click="deleteKP(u)"
									>
										<Trash2Icon class="text-theme-6"/>
									</Tippy>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataKP.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Kelas Private.</p>
          </div>
          <LoadingDiv id="loadingKP" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataKP.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataKP.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataKP.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataKP.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataKP.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataKP.page !== dataKP.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataKP.page !== dataKP.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataKP.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

		<!-- Modal Delete Jenis Soal -->
    <div id="delete-modal-data-kp" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Topik : "{{ exDeleteKP.topik }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-kp')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteKPApi();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";

import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();

    const dataKP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      exDataTopik: {
        topik: "",
        kategori: "",
        content: "",
      },
      listKategori: [],
      msgSuccess: "",
    });

    const titleTable: Array<string> = ["Topik", "Kategori", "Tanggal", "#"];

    const getData = async () => {
      cash("#loadingKP").show();

      const page = dataKP.page;
      const pageSize = dataKP.pageSize;
      const search = dataKP.search;
      axios
        .get(
          api.adminKelasPrivate +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize
					, configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get kp ", rs);

          dataKP.data = rs;
          dataKP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataKP.paginate ", dataKP.paginate);
          cash("#loadingKP").hide();
        })
        .catch( async(error: any) => {
          // console.log("response get kp err ", error.response);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataKP.page = p;
      getData();
    };

    const nextPage = () => {
      dataKP.page = dataKP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataKP.page = dataKP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataKP.page = dataKP.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      dataKP,
      getData,
      router,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
			configGet,
    };
  },
  data() {
    return {
			exDeleteKP: {
				uuid: "",
				topik: ""
			}
		};
  },
  methods: {
    detailKP(data: any) {
      this.$router.push('kelas-private/' + data.uuid)
    },
		deleteKP(data: any): void {
      const ex = this.exDeleteKP;
      ex.uuid = data.uuid;
      ex.topik = data.topik;

      gpf.showModal("#delete-modal-data-kp");
    },
    deleteKPApi() {
      const ex = this.exDeleteKP;

      gpf.gLoading.show();

      axios
        .delete(api.adminKelasPrivate + ex.uuid, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.uuid = "";
          ex.topik = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-kp");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    }
  },
});
</script>
