<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Bidang Soal</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addBS()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <!-- <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="select"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(skd, idx) in fSKD"
                  v-bind:key="idx"
                  :value="skd.slug"
                >
                  {{ skd.fileName }}
                </option>
              </TailSelect>
            </div> -->
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataBS.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataBS.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.jmlSoal }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap uppercase">
                  {{ u.kategori }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap uppercase">
                  {{ u.jenis }} / {{ u.paket }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.isActive ? "Active" : " Suspend" }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.createdAt }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailBSApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editBS(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteBS(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataBS.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Bidang Soal.</p>
          </div>
          <LoadingDiv id="loadingBSadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataBS.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataBS.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataBS.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataBS.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataBS.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataBS.page !== dataBS.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataBS.page !== dataBS.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataBS.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
    <!-- Modal Add and Edit Bidang Soal -->
    <div id="modal-data-bs" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Bidang Soal" : "Edit Bidang Soal" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-bs')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <!-- <div 
            class="flex flex-col sm:flex-row items-center"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Kode"
              v-model="exDataBS.kode"
            />
          </div> -->
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title"
              v-model="exDataBS.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jumlah Soal</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Jumlah Soal"
              v-model="exDataBS.jmlSoal"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataBS.isActive"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div
            class="flex flex-col sm:flex-row items-center mt-3"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kategori Soal</label>
            <div class="w-full sm:w-1/2">
              <TailSelect
                v-model="exDataBS.idCategory"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(ss, idx) in listKodeKS"
                  v-bind:key="idx"
                  :value="ss.value"
                  @click="exDataBS.idCategory = ss.id"
                >
                  {{ ss.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-bs')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addBSApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editBSApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Bidang Soal -->
    <div id="delete-modal-data-bs" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteBS.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-bs')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteBSApi();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail Bidang Soal -->
    <div id="modal-detail-bs" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Bidang Soal</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-bs')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <!-- <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode</label>
            <p class="text-gray-500">{{ detailBS.kode }}</p>
          </div> -->
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <p class="text-gray-500">{{ detailBS.title }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jumlah Soal</label>
            <p class="text-gray-500">{{ detailBS.jmlSoal }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <p class="text-gray-500">{{ detailBS.isActive }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kategori Soal</label>
            <p class="text-gray-500">{{ detailBS.idCategory }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataStatus } from "./bidang-soal";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = dataStatus();

    const titleTable: Array<string> = [
      "Title",
      "Jumlah Soal",
      "Kategori Soal",
      "Jenis Soal",
      "Status",
      "Created",
      "#",
    ];

    const dataBS = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingBSadmin").show();

      const page = dataBS.page;
      const pageSize = dataBS.pageSize;
      const search = dataBS.search;
      axios
        .get(
          api.adminBidangSoal +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataBS.data = rs;
          dataBS.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataBS.paginate ", dataBS.paginate);
          cash("#loadingBSadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingBSadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataBS.page = p;
      getData();
    };

    const nextPage = () => {
      dataBS.page = dataBS.page + 1;
      getData();
    };

    const beforePage = () => {
      dataBS.page = dataBS.page - 1;
      getData();
    };

    const lastPage = () => {
      dataBS.page = dataBS.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataBS,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
    };
  },
  data() {
    return {
      // for modal data bs
      section: "add",
      msgSuccess: "",

      detailBS: {
        id: 0,
        kode: "",
        title: "",
        jmlSoal: "",
        isActive: "1",
        idCategory: "",
      } as any,

      exDataBS: {
        id: 0,
        kode: "",
        title: "",
        jmlSoal: "",
        isActive: "1",
        idCategory: "",
      },

      exDeleteBS: {
        id: 0,
        title: "",
      },

      listKodeKS: [],
    };
  },
  methods: {
    async listKategoriSoalApi(callback: any): Promise<void> {
      gpf.gLoading.show();
      axios
        .get(
          api.adminKategoriSoal,
          this.configGet
        )
        .then(async (result: any) => {
          gpf.gLoading.hide();
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          this.exDataBS.idCategory = rs[0].id;
          this.listKodeKS = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await callback();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async emptyField() {
      const ex = this.exDataBS;
      ex.kode = "";
      ex.title = "";
      ex.jmlSoal = "";
      ex.isActive = "";
      ex.idCategory = "";
    },
    async addBS() {
      await this.emptyField();
      this.section = "add";
      const showMKS = () =>{
        gpf.showModal("#modal-data-bs");
      };
      this.listKategoriSoalApi( showMKS );
    },
    addBSApi() {
      gpf.gLoading.show();
      const ex = this.exDataBS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("title", ex.title);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("isActive", ex.isActive);
      params.append("idCategory", ex.idCategory);

      axios
        .post(api.adminBidangSoal, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-bs");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editBS(data: any) {
      this.section = "edit";
      const showMKS = () =>{
        gpf.showModal("#modal-data-bs");
      };
      this.listKategoriSoalApi( showMKS );
      gpf.gLoading.show();

      axios
        .get(api.adminBidangSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);
          const ex = this.exDataBS;
          ex.id = rs.id;
          ex.kode = rs.kode;
          ex.title = rs.title;
          ex.jmlSoal = rs.jmlSoal;
          ex.isActive = rs.isActive ? "1" : "0";
          ex.idCategory = rs.idCategory.toString();

          await gpf.hideModal("#modal-data-bs");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editBSApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataBS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("title", ex.title);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("isActive", ex.isActive);
      params.append("idCategory", ex.idCategory);

      axios
        .put(api.adminBidangSoal + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-bs");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailBSApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminBidangSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.detailBS;
          ex.id = rs.id;
          ex.kode = rs.kode;
          ex.title = rs.title;
          ex.jmlSoal = rs.jmlSoal;
          ex.isActive = rs.isActive ? "Active" : "Suspend";
          ex.idCategory = rs.SoalCategory.title;

          await gpf.showModal("#modal-detail-bs");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    showPass(id: string) {
      const idHtml = cash("#"+ id);
      if (idHtml.attr("type") === "password") {
        idHtml.attr("type", "text");
      } else {
        idHtml.attr("type", "password");
      }
    },
    deleteBS(data: any): void {
      const ex = this.exDeleteBS;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-bs");
    },
    deleteBSApi() {
      const ex = this.exDeleteBS;

      gpf.gLoading.show();

      axios
        .delete(api.adminBidangSoal + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.title = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-bs");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    }
  },
});
</script>
