<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Sales</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
						<button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addAS()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataAS.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataAS.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.username }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.email }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.phone }}
                </td>
								<td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.convertToCurrency(u.feeSales, 'IDR ') }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.active === 1 ? "Active" : " Suspend" }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.createdAt, "DD MMM, YYYY HH:mm:ss") }}
                </td>
								<td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailASApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editAS(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataAS.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Sales.</p>
          </div>
          <LoadingDiv id="loadingAS" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataAS.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataAS.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataAS.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataAS.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataAS.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataAS.page !== dataAS.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataAS.page !== dataAS.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataAS.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

		<!-- Modal Add and Edit Sales -->
    <div id="modal-data-as" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Sales" : "Edit Sales" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-as')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div 
            class="flex flex-col sm:flex-row items-center"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nama</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Nama"
              v-model="exDataAS.nama"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Email"
              v-model="exDataAS.email"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Phone Number</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Phone Number"
              v-model="exDataAS.phone"
            />
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Username</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="username"
              v-model="exDataAS.username"
            />
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Password</label>
            <div class="relative w-full sm:w-5/6">
              <input
                type="password"
                class="input pr-12 w-full border col-span-10"
                placeholder="Password"
                id="passExDataAS"
                v-model="exDataAS.password"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                @click="$gpf.showPass('passExDataAS')"
              >
                show
              </div>
            </div>
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Fee Sales</label>
            <div class="flex flex-col w-5/6">
              <input
                type="text"
                class="input w-full border flex-1"
                placeholder="Fee Sales"
                v-on:keyup="ctc('feeSales')"
                v-model="exDataAS.feeSales"
              />
              <span class="italic text-sm text-gray-500">Isi dengan nilai Rupiah yang Anda inginkan dari Fee Sales.</span>
            </div>
          </div>
					<div 
						v-if="section === 'edit'"
						class="flex flex-col sm:flex-row items-center mt-3"
					>
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataAS.status"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-as')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addASApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editASApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

		<!-- Modal Detail Sales -->
    <div id="modal-detail-oa" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Sales</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-oa')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nama</label>
            <p class="text-gray-500">{{ detailDataAS.nama }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <p class="text-gray-500">{{ detailDataAS.email }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Phone</label>
            <p class="text-gray-500">{{ detailDataAS.phone }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Username</label>
            <p class="text-gray-500">{{ detailDataAS.username }}</p>
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Fee Sales</label>
            <p class="text-gray-500">{{ $gpf.convertToCurrency(detailDataAS.feeSales, 'IDR ') }}</p>
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Photo</label>
						<img :src="detailDataAS.photo" alt="" class="w-40 max-h-full">
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import { dataStatus } from "./sales";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
		const listStatus = dataStatus();

    const titleTable: Array<string> = [
			"Name",
      "Username",
      "Email",
      "Phone Number",
      "Fee Sales",
			"Status",
      "Created",
			"#",
    ];

    const dataAS = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingAS").show();

      const page = dataAS.page;
      const pageSize = dataAS.pageSize;
      const search = dataAS.search;
      axios
        .get(
          api.affSales +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataAS.data = rs;
          dataAS.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataAS.paginate ", dataAS.paginate);
          cash("#loadingAS").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingAS").hide();
          // console.log("response get du err ", error.response);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataAS.page = p;
      getData();
    };

    const nextPage = () => {
      dataAS.page = dataAS.page + 1;
      getData();
    };

    const beforePage = () => {
      dataAS.page = dataAS.page - 1;
      getData();
    };

    const lastPage = () => {
      dataAS.page = dataAS.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataAS,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
			listStatus,
    };
  },
  data() {
    return {
			section: "add",

			exDataAS: {
				id: "",
				nama: "",
				email: "",
				phone: "",
				username: "",
				password: "",
				feeSales: "",
				status: "1",
			},

			detailDataAS: {
				id: "",
				nama: "",
				email: "",
				phone: "",
				username: "",
				feeSales: "",
				photo: "",
			},

		};
  },
  methods: {
		async emptyField() {
			const ex = this.exDataAS;
			ex.id = "0";
			ex.nama = "";
			ex.email = "";
			ex.phone = "";
			ex.username = "";
			ex.password = "";
			ex.feeSales = "";
		},
		ctc(f: string) {
      const ex: any = this.exDataAS;
      ex[f] = gpf.ctc(ex[f]);
    },
		async addAS() {
      await this.emptyField();
      this.section = "add";
			gpf.showModal("#modal-data-as");
    },
    addASApi() {
      gpf.gLoading.show();
      const ex = this.exDataAS;
      const params = new URLSearchParams();
      params.append("nama", ex.nama);
      params.append("email", ex.email);
      params.append("phone", ex.phone);
      params.append("username", ex.username);
      params.append("password", ex.password);
      params.append("feeSales", gpf.btc(ex.feeSales));

      axios
        .post(api.affSales, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-as");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editAS(data: any) {
      this.section = "edit";
      gpf.gLoading.show();

      axios
        .get(api.affSales + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);
          const ex = this.exDataAS;
          ex.id = rs.id;
          ex.nama = rs.name;
          ex.email = rs.email;
          ex.phone = rs.phone;
          ex.username = rs.username;
          ex.feeSales = gpf.ctc(rs.feeSales);
					ex.status = rs.status === 'Active' ? '1' : '0';

          await gpf.showModal("#modal-data-as");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editASApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataAS;
      const params = new URLSearchParams();
      params.append("nama", ex.nama);
      params.append("email", ex.email);
      params.append("phone", ex.phone);
      params.append("username", ex.username);
      params.append("password", ex.password);
      params.append("feeSales", gpf.btc(ex.feeSales));

      axios
        .put(api.affSales + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-as");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailASApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.affSales + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.detailDataAS;
          ex.id = rs.id;
          ex.nama = rs.name;
          ex.email = rs.email;
          ex.phone = rs.phone;
          ex.username = rs.username;
          ex.feeSales = rs.feeSales;
          ex.photo = rs.photo;

          await gpf.showModal("#modal-detail-oa");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },

  },
});
</script>
