<template>
  <div>
    <!-- <DarkModeSwitcher /> -->
    <MobileMenu />
    <div class="flex mode-mobile">
      <!-- BEGIN: Side Menu -->
      <nav class="side-nav">
        <!-- BEGIN: Logo -->
        <router-link
          :to="{ name: 'home' }"
          tag="a"
          class="intro-x flex items-center pl-5 pt-4"
        >
          <img
            alt="Cpnsonline Tailwind HTML Admin Template"
            class="w-10 max-h-10"
            src="@/assets/images/cpnsonline-new-logo.png"
          />
          <span class="hidden xl:block text-white text-lg ml-3">
            CPNS<span class="font-medium">online</span>
          </span>
        </router-link>
        <!-- END: Logo -->
        <div class="side-nav__devider mt-3 mb-4"></div>
        <ul>
          <!-- BEGIN: First Child -->
          <template v-for="(menu, menuKey) in formattedMenu">
            <li
              v-if="menu == 'devider'"
              :key="menu + menuKey"
              class="side-nav__devider my-6"
            ></li>
            <li v-else :key="menu + menuKey">
              <SideMenuTooltip
                tag="a"
                :content="menu.title"
                href="javascript:;"
                class="side-menu"
                :class="{
                  'side-menu--active': menu.active,
                  'side-menu--open': menu.activeDropdown,
                }"
                @click="linkTo(menu, router, role)"
              >
                <div class="side-menu__icon">
                  <component :is="menu.icon" />
                </div>
                <div class="side-menu__title">
                  {{ menu.title }}
                  <div
                    v-if="menu.subMenu"
                    class="side-menu__sub-icon"
                    :class="{ 'transform rotate-180': menu.activeDropdown }"
                  >
                    <ChevronDownIcon />
                  </div>
                </div>
              </SideMenuTooltip>
              <!-- BEGIN: Second Child -->
              <transition @enter="enter" @leave="leave">
                <ul v-if="menu.subMenu && menu.activeDropdown">
                  <li
                    v-for="(subMenu, subMenuKey) in menu.subMenu"
                    :key="subMenuKey"
                  >
                    <SideMenuTooltip
                      tag="a"
                      :content="subMenu.title"
                      href="javascript:;"
                      class="side-menu"
                      :class="{ 'side-menu--active': subMenu.active }"
                      @click="linkTo(subMenu, router, role)"
                    >
                      <div class="side-menu__icon">
                        <component :is="subMenu.icon" />
                        <!-- <ActivityIcon /> -->
                      </div>
                      <div class="side-menu__title">
                        {{ subMenu.title }}
                        <div
                          v-if="subMenu.subMenu"
                          class="side-menu__sub-icon"
                          :class="{
                            'transform rotate-180': subMenu.activeDropdown,
                          }"
                        >
                          <ChevronDownIcon />
                        </div>
                      </div>
                    </SideMenuTooltip>
                    <!-- BEGIN: Third Child -->
                    <transition @enter="enter" @leave="leave">
                      <ul v-if="subMenu.subMenu && subMenu.activeDropdown">
                        <li
                          v-for="(
                            lastSubMenu, lastSubMenuKey
                          ) in subMenu.subMenu"
                          :key="lastSubMenuKey"
                        >
                          <SideMenuTooltip
                            tag="a"
                            :content="lastSubMenu.title"
                            href="javascript:;"
                            class="side-menu"
                            :class="{ 'side-menu--active': lastSubMenu.active }"
                            @click="linkTo(lastSubMenu, router, role)"
                          >
                            <div class="side-menu__icon">
                              <ZapIcon />
                            </div>
                            <div class="side-menu__title">
                              {{ lastSubMenu.title }}
                            </div>
                          </SideMenuTooltip>
                        </li>
                      </ul>
                    </transition>
                    <!-- END: Third Child -->
                  </li>
                </ul>
              </transition>
              <!-- END: Second Child -->
            </li>
          </template>
          <!-- END: First Child -->
        </ul>
        <div class="side-nav__devider mt-3 mb-4"></div>
        <DarkModeSwitcherMenu />
      </nav>
      <!-- END: Side Menu -->
      <!-- BEGIN: Content -->
      <div class="content">
        <TopBar />
        <router-view />
      </div>
      <!-- END: Content -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted, ref, watch, reactive } from "vue";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "@/store";
import { helper as $h } from "@/utils/helper";
import TopBar from "@/components/top-bar/Main.vue";
import MobileMenu from "@/components/mobile-menu/Main.vue";
import DarkModeSwitcher from "@/components/dark-mode-switcher/Main.vue";
import SideMenuTooltip from "@/components/side-menu-tooltip/Main.vue";
import { linkTo, nestedMenu, enter, leave } from "./index";
import DarkModeSwitcherMenu from "@/components/dark-mode-switcher-menu/Main.vue";
import { api } from "@/utils/api-config";
import { VueCookieNext as $ck } from "vue-cookie-next";

export default defineComponent({
  components: {
    TopBar,
    MobileMenu,
    DarkModeSwitcher,
    SideMenuTooltip,
    DarkModeSwitcherMenu,
  },
  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const formattedMenu = ref([]);
    const role: string = api.getRole();

    const menuList = reactive({
      listMenu: $ck.getCookie("list-menu"),
    })

    const getListMenu = () : any => {
      const listMenu = JSON.parse( menuList.listMenu );
      return listMenu;
      // if ( role === "member" ) {
      //   return store.state.sideMenu.member;
      // } else if ( role === "admin" ) {
      //   return store.state.sideMenu.admin;
      // } else if ( role === "affiliate" ) {
      //   return store.state.sideMenu.affiliate;
      // } else if ( role === "sales" ) {
      //   return store.state.sideMenu.sales;
      // } else if ( role === "keuangan" ) {
      //   return store.state.sideMenu.keuangan;
      // } else if ( role === "tentor" ) {
      //   return store.state.sideMenu.tentor;
      // } else if ( role === "owner" ) {
      //   return store.state.sideMenu.owner;
      // } else {
      //   return store.state.sideMenu.member;
      // }
    }

    // const listMenu = getListMenu();
    const sideMenu = computed(() =>
      nestedMenu( getListMenu(), route, role)
    );

    watch(
      computed(() => route.path),
      () => {
        formattedMenu.value = $h.toRaw(sideMenu.value);
        // console.log('formattedMenu.value ', formattedMenu.value);
      }
    );

    onMounted(() => {
      cash("body")
        .removeClass("error-page")
        .removeClass("login")
        .addClass("app");
      formattedMenu.value = $h.toRaw(sideMenu.value);
    });

    return {
      formattedMenu,
      router,
      linkTo,
      enter,
      leave,
      role,
    };
  },
});
</script>

<style lang="scss" scoped>
@media (max-width: 640px) {
  .mode-mobile {
    margin: 0 -1em;
  }
}
</style>
