<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Leaderboard</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <p
              class="text-base mr-auto md:mr-2 mb-1 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <Litepicker
                v-model="dataLB.dateRange"
                :options="{
                  autoApply: false,
                  singleMode: false,
                  numberOfColumns: 2,
                  numberOfMonths: 2,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input w-56 border block"
              />
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-2"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataLB.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataLB.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="w-12 h-12 rounded-full overflow-hidden shadow-lg image-fit zoom-in">
                    <img class="w-full" :src="u.photo" alt="photo-user" :title="'Photo ' + u.name" data-action="zoom">
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.jumlah }}
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataLB.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Leaderboard.</p>
          </div>
          <LoadingDiv id="loadingDataLB" />
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { helper as $h} from "@/utils/helper";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();

    const titleTable: Array<string> = [
      "Photo",
      "Name",
      "Jumlah",
    ];

    const dataLB = reactive({
      tempData: [] as any,
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
        dataPaginate: [],
      } as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataLB.dateRange = `${$h.formatDate(firstDay.toString(), 'DD MMM, YYYY')} - ${$h.formatDate(lastDay.toString(), 'DD MMM, YYYY')}`;

    const getData = async () => {
      cash("#loadingDataLB").show();

      const since = $h.formatDate(dataLB.dateRange.split('-')[0], 'YYYY-MM-DD');
      const until = $h.formatDate(dataLB.dateRange.split('-')[1], 'YYYY-MM-DD');

      const url =
        api.salesLeaderboard +
        "?since=" +
        since +
        "&until=" +
        until;
      const hide = () => cash("#loadingDataLB").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;

      dataLB.data = rs;

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataLB.page = p;
      getData();
    };

    const nextPage = () => {
      dataLB.page = dataLB.page + 1;
      getData();
    };

    const beforePage = () => {
      dataLB.page = dataLB.page - 1;
      getData();
    };

    const lastPage = () => {
      dataLB.page = dataLB.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataLB,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
    };
  },
  data() {
    return {};
  },
  methods: {
  },
});
</script>
