<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Materi</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addMateri()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="dataMateri.type"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(f, idx) in filterTypes"
                  v-bind:key="idx"
                  :value="f.value"
                >
                  {{ f.title }}
                </option>
              </TailSelect>
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-2"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataMateri.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataMateri.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <div class="w-36">
                    <img
                      class="w-full"
                      :src="u.banner"
                      alt="Banner Image"
                      :title="'Banner ' + u.title"
                    />
                  </div>
                </td>
                <td class="border-b dark:border-dark-5">
                  {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 truncate max-w-72">
                  <a :href="u.content" target="_blank"> {{ u.content }} </a>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.createdAt }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailMateriApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editMateri(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteMateri(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataMateri.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Materi.</p>
          </div>
          <LoadingDiv id="loadingDMadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataMateri.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataMateri.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataMateri.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataMateri.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataMateri.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataMateri.page !== dataMateri.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataMateri.page !== dataMateri.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataMateri.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
    <!-- Modal Add and Edit Materi -->
    <div id="modal-data-materi" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Materi" : "Edit Materi" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-materi')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title Materi"
              v-model="exDataMateri.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Banner</label>
            <div class="flex flex-col w-10/12">
              <div class="flex items-center mb-2">
                <input
                  type="file"
                  class="button w-10 border flex-1 hidden"
                  placeholder="Banner"
                  id="fileBannerDM"
                  v-on:change="handleFile($event)"
                />
                <button
                  v-if="exDataMateri.banner === ''"
                  @click="chooseBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
                >
                  Choose File
                </button>
                <button
                  v-else
                  @click="removeBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-32 whitespace-nowrap"
                >
                  Remove Banner
                </button>
                <span class="text-sm ml-2 truncate">{{ exDataMateri.nameBanner }}</span>
              </div>
              <span class="text-sm">Maks Ukuran File Banner = 1MB.</span>
              <span
                v-if="exDataMateri.oversizeBanner"
                class="text-sm text-red-700"
              >Ukuran Banner melebihi 1MB, Silahkan Pilih Banner kembali.</span>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Preview Image</label>
            <div class="show-image w-80">
              <img
                v-if="section === 'add'"
                id="showImage"
                src
                alt
              />
              <img
                v-if="section === 'edit'"
                id="showImage"
                :src="exDataMateri.banner"
                alt
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Content</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Content"
              v-model="exDataMateri.content"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Type</label>
            <div class="min-w-32 flex flex-row">
              <TailSelect
                v-model="exDataMateri.type"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(f, idx) in filterTypes"
                  v-bind:key="idx"
                  :value="f.value"
                >
                  {{ f.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <div class="min-w-32 flex flex-row">
              <TailSelect
                v-model="exDataMateri.isActive"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(f, idx) in isActiveMateri"
                  v-bind:key="idx"
                  :value="f.value"
                >
                  {{ f.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-materi')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addMateriApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editMateriApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Materi -->
    <div id="delete-modal-data-materi" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteMateri.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-materi')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteMateriApi()"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail Materi -->
    <div id="modal-detail-materi" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Materi</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-materi')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <p class="text-gray-500">{{ detailMateri.title }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Banner</label>
            <div class="show-image w-60">
              <img
                id="showImageDetail"
                :src="detailMateri.banner"
                alt
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Content</label>
            <a :href="detailMateri.content" target="_blank" class="text-gray-500 truncate max-w-9/12">{{ detailMateri.content }}</a>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Type</label>
            <p class="text-gray-500">{{ detailMateri.type }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <p class="text-gray-500">{{ detailMateri.isActive }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const config = api.configGet();

    const titleTable: Array<string> = [
      "Banner",
      "Title",
      "Content",
      "Created",
      "#",
    ];

    const filterTypes = [
      {
        title: "PDF",
        value: "pdf",
      },
      {
        title: "Video",
        value: "video",
      },
    ];

    const isActiveMateri = [
      {
        title: "Suspend",
        value: "0",
      },
      {
        title: "Active",
        value: "1",
      },
    ];

    const dataMateri = reactive({
      data: [] as any,
      page: 1,
      type: "pdf",
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingDMadmin").show();

      const search = dataMateri.search;
      const type = dataMateri.type;
      const page = dataMateri.page;
      const pageSize = dataMateri.pageSize;
      axios
        .get(
          api.adminDataMateri +
            "?search=" +
            search +
            "&types=" +
            type +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get dm ", rs);

          dataMateri.data = rs;
          dataMateri.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataMateri.paginate ", dataMateri.paginate);
          cash("#loadingDMadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDMadmin").hide();
          // console.log("response get dm err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataMateri.page = p;
      getData();
    };

    const nextPage = () => {
      dataMateri.page = dataMateri.page + 1;
      getData();
    };

    const beforePage = () => {
      dataMateri.page = dataMateri.page - 1;
      getData();
    };

    const lastPage = () => {
      dataMateri.page = dataMateri.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataMateri,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      config,
      filterTypes,
      isActiveMateri,
    };
  },
  data() {
    return {
      // for modal data materi
      section: "add",

      detailMateri: {
        id: 0,
        title: "",
        banner: "",
        content: "",
        type: "",
        isActive: "",
      } as any,

      exDataMateri: {
        id: 0,
        title: "",
        banner: "",
        nameBanner: "Choose File",
        oversizeBanner: false,
        tempBanner: "",
        content: "",
        type: "pdf",
        isActive: "1",
      },

      exDeleteMateri: {
        id: 0,
        title: "",
      },
    };
  },
  methods: {
    chooseBanner() {
      const id = "fileBannerDM";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBanner() {
      const ex = this.exDataMateri;
      ex.nameBanner = "Choose File";
      ex.banner = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataMateri;

      if (sizeFiles > 1000000) {
        ex.oversizeBanner = true;
        this.removeBanner();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBanner = nameFile;
        ex.banner = files[0];
        ex.oversizeBanner = false;
        // const bodyFormData = new FormData();
        // bodyFormData.append("files", files[0]);

        this.renderImage(target.files[0]);
      }
      // console.log("evt ", evt);
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    async emptyField() {
      const ex = this.exDataMateri;
      ex.id = 0;
      ex.title = "";
      ex.banner = "";
      ex.nameBanner = "Choose File";
      ex.oversizeBanner = false;
      ex.tempBanner = "";
      ex.content = "";
      ex.type = "pdf";
      ex.isActive = "1";
    },
    async addMateri() {
      await this.emptyField();
      this.section = "add";
      gpf.showModal("#modal-data-materi");
    },
    addMateriApi() {
      gpf.gLoading.show();
      const ex = this.exDataMateri;
      const body = new FormData();
      body.append("title", ex.title);
      body.append("banner", ex.banner);
      body.append("content", ex.content);
      body.append("types", ex.type);
      body.append("isActive", ex.isActive);

      axios
        .post(api.adminDataMateri, body, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-materi");
          await this.emptyField();
          await this.getData();
        })
        .catch(async(error: any) => {
          gpf.gLoading.hide();
          // console.log("response add dm err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editMateri(data: any) {
      this.section = "edit";

      gpf.gLoading.show();

      axios
        .get(api.adminDataMateri + data.slug, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.exDataMateri;
          ex.id = rs.id;
          ex.title = rs.title;
          ex.banner = rs.banner;
          ex.nameBanner = rs.banner;
          ex.tempBanner = rs.banner;
          ex.content = rs.content;
          ex.type = rs.types;
          ex.isActive = rs.isActive;

          await gpf.showModal("#modal-data-materi");
        })
        .catch(async(error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editMateriApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataMateri;
      const body = new FormData();
      body.append("title", ex.title);
      if ( ex.nameBanner !== ex.tempBanner ) {
        body.append("banner", ex.banner);
      } else {
        body.append("banner", "");
      }
      body.append("content", ex.content);
      body.append("types", ex.type);
      body.append("isActive", ex.isActive);

      axios
        .put(api.adminDataMateri + ex.id, body, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-materi");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    detailMateriApi(data: any): void {
      gpf.gLoading.show();
      const exd = this.detailMateri;

      axios
        .get(api.adminDataMateri + data.slug, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);
          exd.title = rs.title;
          exd.banner = rs.banner;
          exd.content = rs.content;
          exd.type = rs.types === "pdf" ? "PDF" : "Video";
          exd.isActive = rs.isActive ? "Active" : "Suspend";

          await gpf.showModal("#modal-detail-materi");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    deleteMateri(data: any): void {
      const ex = this.exDeleteMateri;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-materi");
    },
    deleteMateriApi() {
      const ex = this.exDeleteMateri;

      gpf.gLoading.show();

      axios
        .delete(api.adminDataMateri + ex.id, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.title = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-materi");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
  },
});
</script>
