
import { defineComponent, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const config = api.configGet();

    const dataMV = reactive({
      data: [] as any,
      page: 1,
      pageSize: 12,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingMV").show();

      const page = dataMV.page;
      const pageSize = dataMV.pageSize;
      const search = dataMV.search;
      axios
        .get(
          api.memberMateri + 'video/' +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          console.log("response get mp ", rs);

          dataMV.data = rs;
          dataMV.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataMV.paginate ", dataMV.paginate);
          cash("#loadingMV").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingMV").hide();
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataMV.page = p;
      getData();
    };

    const nextPage = () => {
      dataMV.page = dataMV.page + 1;
      getData();
    };

    const beforePage = () => {
      dataMV.page = dataMV.page - 1;
      getData();
    };

    const lastPage = () => {
      dataMV.page = dataMV.paginate.totalPages;
      getData();
    };

    return {
      getData,
      dataMV,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      config,
    };
  },
});
