            </div>
            <div class="flex flex-col sm:flex-row items-center">
              <label class="hidden sm:block sm:w-28 sm:text-right sm:mr-5"></label>
              <div v-if="required.memberId" class="text-theme-6 my-1 ml-1">This field is required</div>
            </div>
          </div>
          <div 
            id="tableSearchMember"
            class="flex flex-col items-center p-2 border border-gray-500 rounded-md mt-2"
            style="display: none;"
          >
            <div class="w-full flex flex-row items-center ">
              <label class="text-center">Silahkan Pilih Member.</label>
              <a
                href="javascript:;"
