                      </td>
                      <td class="border-b dark:border-dark-5 whitespace-nowrap">
                        {{ u.phone }}
                      </td>
                      <td class="border-b dark:border-dark-5 whitespace-nowrap">
                        {{ u.active === 1 ? "Active" : " Suspend" }}
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div
                  v-if="dataSM.data.length === 0"
                  class="w-full p-5 flex items-center justify-center"
                >
                  <p class="text-base font-bold">Tidak Ada Data Member.</p>
                </div>
                <LoadingDiv id="loadingSM" style="display: none;" />
              </div>
              <!-- BEGIN: Pagination -->
              <div
                v-if="dataSM.data.length !== 0"
                class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-3"
              >
                <ul class="pagination">
                  <li v-if="dataSM.page !== 1" @click="goToPageSM(1)">
                    <span class="pagination__link">
                      <ChevronsLeftIcon class="w-4 h-4" />
                    </span>
                  </li>
                  <li v-if="dataSM.page !== 1" @click="beforePageSM()">
                    <span class="pagination__link">
                      <ChevronLeftIcon class="w-4 h-4" />
                    </span>
                  </li>
                  <li v-for="(p, idx) in dataSM.paginate.pages" :key="idx">
                    <span
                      :class="[
                        'pagination__link cursor-pointer',
                        dataSM.page === p ? 'pagination__link--active' : '',
                      ]"
                      @click="goToPageSM(p)"
                      >{{ p }}</span
                    >
                  </li>
                  <li
                    v-if="dataSM.page !== dataSM.paginate.totalPages"
                    @click="nextPageSM()"
                  >
                    <span class="pagination__link">
                      <ChevronRightIcon class="w-4 h-4" />
                    </span>
                  </li>
                  <li
                    v-if="dataSM.page !== dataSM.paginate.totalPages"
                    @click="lastPageSM()"
                  >
                    <span class="pagination__link">
                      <ChevronsRightIcon class="w-4 h-4" />
                    </span>
                  </li>
                </ul>
              </div>
              <!-- END: Pagination -->
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-28 sm:text-right sm:mr-5"
              >Promo</label
            >
            <div class="w-full md:w-5/6">
              <TailSelect
                v-model="exDataNewOrder.promoId"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listPromoId"
                  v-bind:key="idx"
                  :value="s.id"
                  @click="exDataNewOrder.promoId = s.id"
                >
                  <span v-if="s.type === 'percent'">
                    {{ s.code }} | {{ s.discount }}%
                  </span>
                  <span v-if="s.type === 'price'">
                    {{ s.code }} |
                    {{ $gpf.convertToCurrency(s.discount, "IDR ") }}
                  </span>
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-28 sm:text-right sm:mr-5"
              >Paket</label
            >
            <div class="w-full md:w-5/6">
              <TailSelect
                v-model="exDataNewOrder.packageId"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listPackageId"
                  v-bind:key="idx"
                  :value="s.id"
                  @click="exDataNewOrder.packageId = s.id"
                >
                  {{ s.title }} | {{ $gpf.convertToCurrency(s.price, "IDR ") }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-28 sm:text-right sm:mr-5"
              >Payment Method</label
            >
            <div class="w-full md:w-5/6">
              <TailSelect
                v-model="exDataNewOrder.paymentId"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <optgroup
                  v-for="(pp, idx) in listPaymentId"
                  v-bind:key="idx"
                  :label="pp.label"
                >
                  <option
                    v-for="(ss, idx) in pp.options"
                    v-bind:key="idx"
                    :value="ss.value"
                    @click="exDataNewOrder.paymentId = ss.value"
                  >
                    {{ ss.text }}
                  </option>
                </optgroup>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-28 sm:text-right sm:mr-5"
              >Payment Type</label
            >
            <div class="w-full md:w-5/6">
              <TailSelect
                v-model="exDataNewOrder.paymentType"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listPaymentType"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <!-- <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Price</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Price"
              v-on:keyup="ctcNO('price')"
              v-model="exDataNewOrder.price"
            />
          </div> -->
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-no')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addNOApi()"
          >
            Simpan
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Add New Order -->
    <div id="modal-data-ko" class="modal p-0">
      <div class="modal__content modal__content--xl">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Konfirmasi Order</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-no')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Tanggal Transaksi</label>
            <div class="relative w-5/6">
              <div
                class="absolute rounded-l w-10 h-full flex items-center justify-center bg-gray-100 border text-gray-600 dark:bg-dark-1 dark:border-dark-4"
              >
                <CalendarIcon class="w-4 h-4" />
              </div>
              <Litepicker
                v-model="exDataConfirmOrder.tglTransaksi"
                :options="{
                  autoApply: false,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input pl-12 border text-center"
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Jumlah Transaksi</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Jumlah Transaksi"
              v-on:keyup="ctcKO('jmlTransaksi')"
              v-model="exDataConfirmOrder.jmlTransaksi"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">No Rekning</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="No Rekening"
              v-model="exDataConfirmOrder.noRekening"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Nama Pengirim</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Nama Pengirim"
              v-model="exDataConfirmOrder.namaPengirim"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-28 sm:text-right sm:mr-5"
              >Bank Tujuan</label
            >
            <div class="w-full md:w-5/6">
              <TailSelect
                v-model="exDataConfirmOrder.bankTujuan"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <optgroup
                  v-for="(pp, idx) in listPaymentId"
                  v-bind:key="idx"
                  :label="pp.label"
                >
                  <option
                    v-for="(ss, idx) in pp.options"
                    v-bind:key="idx"
                    :value="ss.value"
                    @click="exDataConfirmOrder.bankTujuan = ss.value"
                  >
                    {{ ss.text }}
                  </option>
                </optgroup>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Bukti Transfer</label>
            <div class="flex flex-col w-10/12">
              <div class="flex items-center mb-2">
                <input
                  type="file"
                  class="button w-10 border flex-1 hidden"
                  placeholder="Bukti Transfer"
                  id="fileBT"
                  v-on:change="handleFile($event)"
                />
                <button
                  v-if="exDataConfirmOrder.buktiTransfer === ''"
                  @click="chooseBT()"
                  class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
                >
                  Choose File
                </button>
                <button
                  v-else
                  @click="removeBT()"
                  class="button bg-theme-1 hover:bg-primary text-white w-32 whitespace-nowrap"
                >
                  Remove File
                </button>
                <span class="text-sm ml-2 truncate">{{ exDataConfirmOrder.nameBT }}</span>
              </div>
              <span class="text-sm">Maks Ukuran File Image Upload = 1MB.</span>
              <span
                v-if="exDataConfirmOrder.oversizeBT"
                class="text-sm text-red-700"
              >Ukuran BT melebihi 1MB, Silahkan Pilih Image kembali.</span>
            </div>
          </div>
          <div 
            v-if="exDataConfirmOrder.buktiTransfer"
            class="flex flex-col sm:flex-row items-center mt-3"
          >
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Preview Image</label>
            <div class="show-image w-80">
              <img
                id="showImage"
                :src="exDataConfirmOrder.buktiTransfer"
                alt
              />
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-ko')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="confirmOrderApi()"
          >
            Simpan
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";
import { dataPaymentType } from "./order";
import { helper as $h } from "@/utils/helper";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listPaymentType = dataPaymentType();

    const titleTable: Array<string> = [
      "Invoice",
      "Name",
      "Phone Number",
      "Email",
      "Price",
      "Paket",
      "Status",
      "Payment Status",
      "Nomor VA",
      "Created",
    ];

    const dataOrder = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingDMaff").show();

      const page = dataOrder.page;
      const pageSize = dataOrder.pageSize;
      const search = dataOrder.search;
      axios
        .get(
          api.salesOrder +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataOrder.data = rs;
          dataOrder.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataOrder.paginate ", dataOrder.paginate);
          cash("#loadingDMaff").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDMaff").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataOrder.page = p;
      getData();
    };

    const nextPage = () => {
      dataOrder.page = dataOrder.page + 1;
      getData();
    };

    const beforePage = () => {
      dataOrder.page = dataOrder.page - 1;
      getData();
    };

    const lastPage = () => {
      dataOrder.page = dataOrder.paginate.totalPages;
      getData();
    };

    // ==========================================================
    const titleTableSM: Array<string> = [
      "Name",
      "Username",
      "Email",
      "Phone Number",
      "Status",
    ];

    const dataSM = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getDataSM = async () => {
      cash("#loadingSM").show();

      const page = dataSM.page;
      const pageSize = dataSM.pageSize;
      const search = dataSM.search;
      axios
        .get(
          api.salesMember +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataSM.data = rs;
          dataSM.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataSM.paginate ", dataSM.paginate);
          cash("#loadingSM").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingSM").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };

    const changePSSM = () => {
      getDataSM();
    };

    const goToPageSM = (p: number) => {
      dataSM.page = p;
      getDataSM();
    };

    const nextPageSM = () => {
      dataSM.page = dataSM.page + 1;
      getDataSM();
    };

    const beforePageSM = () => {
      dataSM.page = dataSM.page - 1;
      getDataSM();
    };

    const lastPageSM = () => {
      dataSM.page = dataSM.paginate.totalPages;
      getDataSM();
    };
    // ==========================================================

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataOrder,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listPaymentType,

      // ================================
      titleTableSM,
      dataSM,
      getDataSM,
      changePSSM,
      goToPageSM,
      nextPageSM,
      beforePageSM,
      lastPageSM,
      // ================================
    };
  },
  data() {
    return {
      exDataNewOrder: {
        memberId: "",
        promoId: "",
        packageId: "",
        paymentId: "",
        // price: "",
        paymentType: "",
      },
      listPromoId: [] as any,
      listPackageId: [] as any,
      listPaymentId: [] as any,
      required: {
        memberId: false,
      },

      exDataConfirmOrder: {
        invoice: "",
        tglTransaksi: "",
        jmlTransaksi: "",
        noRekening: "",
        namaPengirim: "",
        bankTujuan: "",
        buktiTransfer: "",
        nameBT: "Choose File",
        oversizeBT: false,
        tempBT: "",
      }
    };
  },
  methods: {
    chooseBT() {
      const id = "fileBT";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBT() {
      const ex = this.exDataConfirmOrder;
      ex.nameBT = "Choose File";
      ex.buktiTransfer = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataConfirmOrder;

      if (sizeFiles > 1000000) {
        ex.oversizeBT = true;
        this.removeBT();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBT = nameFile;
        ex.buktiTransfer = files[0];
        ex.oversizeBT = false;
        // const bodyFormData = new FormData();
        // bodyFormData.append("files", files[0]);

        this.renderImage(target.files[0]);
      }
      // console.log("evt ", evt);
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    showTableMember() {
      cash("#tableSearchMember").show();
    },
    hideTableMember() {
      cash("#tableSearchMember").hide();
    },
    async emptyField() {
      const ex = this.exDataNewOrder;
      ex.memberId = "";
      ex.promoId = "";
      ex.packageId = "";
      ex.paymentId = "";
      // ex.price = "";
      ex.paymentType = "";
    },
    ctcNO(f: string) {
      const ex: any = this.exDataNewOrder;
      ex[f] = gpf.ctc(ex[f]);
    },
    ctcKO(f: string) {
      const ex: any = this.exDataConfirmOrder;
      ex[f] = gpf.ctc(ex[f]);
    },
    chooseMember(data: any) {
      this.exDataNewOrder.memberId = data.id;
      this.dataSM.search = data.name;

      setTimeout(() => {
        this.hideTableMember();
      }, 1000);
    },
    async addNO() {
      await gpf.gLoading.show();

      this.listPromoId = await api.getPromo();
      this.listPackageId = await api.getPackage();
      this.listPaymentId = await api.getPayment();

      const ex = this.exDataNewOrder;
      ex.promoId = this.listPromoId[0].id;
      ex.packageId = this.listPackageId[0].id;
      ex.paymentId = this.listPaymentId[0].options[0].value;
      ex.paymentType = this.listPaymentType[0].value;

      await gpf.showModal("#modal-data-no");
      await gpf.gLoading.hide();
    },
    checkValidationNO() {
      const rq = this.required;
      const ex = this.exDataNewOrder;
      if ( !ex.memberId || !this.dataSM.search ) {
        rq.memberId = true;
        return false;
      } else {
        rq.memberId = false;
        return true;
      }
    },
    addNOApi() {
      const check = this.checkValidationNO();
      if ( check ) {
        
        gpf.gLoading.show();
        const ex = this.exDataNewOrder;
        const params = new URLSearchParams();
        params.append("memberId", ex.memberId);
        params.append("promoId", ex.promoId);
        params.append("packageId", ex.packageId);
        params.append("paymentId", ex.paymentId);
        params.append("paymentType", ex.paymentType);
        // params.append("price", gpf.btc(ex.price));

        axios
          .post(api.salesOrder, params, this.configXwfu)
          .then(async (result: any) => {
            gpf.gLoading.hide();

            await gpf.handleSds(result);
            await gpf.hideModal("#modal-data-no");
            await this.emptyField();
            await this.getData();
          })
          .catch(async (error: any) => {
            gpf.gLoading.hide();
            await gpf.handleEds(error);
          });

      }
    },
    seeInfoVA(data: any) {
      gpf.gLoading.show();
      const inv = data.invoice;
      const va = data.nomorVA;

      axios
        .get(
          api.salesStatusOrder +
            "?invoice=" +
            inv +
            "&nomorVA=" +
            va,
          this.configGet
        )
        .then(async (result: any) => {
          gpf.gLoading.hide();
          const ds = result.data;
          // console.log("response get du ", rs);

          await gpf.handleSds(result);
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();

          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    },
    async confirmOrder(data: any) {
      gpf.gLoading.show();
      const ex = this.exDataConfirmOrder;
      const dateNow = new Date();

      ex.invoice = data.invoice;
      ex.jmlTransaksi = "";
      ex.noRekening = "";
      ex.namaPengirim = "";
      ex.buktiTransfer = "";
      ex.nameBT = "Choose File";
      ex.oversizeBT = false;
      ex.tempBT = "";
      ex.tglTransaksi = $h.formatDate(dateNow.toString(), "DD MMM, YYYY");

      this.listPaymentId = await api.getPayment();
      ex.bankTujuan = this.listPaymentId[0].options[0].value;

      gpf.showModal("#modal-data-ko");
      gpf.gLoading.hide();
    },
    confirmOrderApi() {
      gpf.gLoading.show();
      const ex = this.exDataConfirmOrder;
      const body = new FormData();
      body.append("tglTransaksi", $h.formatDate(ex.tglTransaksi, "YYYY-MM-DD"));
      body.append("jmlTransaksi", gpf.btc(ex.jmlTransaksi));
      body.append("noRekening", ex.noRekening);
      body.append("namaPengirim", ex.namaPengirim);
      body.append("bankTujuan", ex.bankTujuan);
      body.append("buktiTransfer", ex.buktiTransfer);

      axios
        .post(api.salesConfirm + ex.invoice, body, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-ko");
          await this.emptyField();
          await this.getData();
        })
        .catch(async(error: any) => {
          gpf.gLoading.hide();
          // console.log("response add dm err ", error.response);
          await gpf.handleEds(error);
        });
    },
  },
});
</script>
