<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex sm:flex-row items-center h-auto sm:h-10">
          <h2 class="text-lg font-medium truncate">Kelas Private</h2>
          <a
            href=""
            class="ml-auto mt-2 sm:mt-0 flex text-theme-1 dark:text-theme-10 whitespace-nowrap"
            @click="getData();"
          >
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> Reload Data
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div class="flex md:flex-row items-center col-span-3 md:col-span-2">
            <button
              @click="getKategoriKelas();"
              class="button flex flex-row items-center bg-theme-1 hover:bg-blue-700 text-white w-auto pl-0 shadow-md mb-2 md:mb-0"
            >
              <PlusIcon class="w-6 h-6 mx-2 font-bold" />
              <span class="inline-block">Topik Baru</span>
            </button>
          </div>
          <div class="w-full col-span-3 md:col-span-1">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataKP.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataKP.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.topik }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.kategori }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.tanggal }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <Tippy
                    tag="span"
                    class="button inline-block text-white"
                    content="View Respon"
                    @click="detailKP(u)"
                  >
                    <EyeIcon class="text-primary" />
                  </Tippy>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataKP.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Kelas Private.</p>
          </div>
          <LoadingDiv id="loadingKP" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataKP.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataKP.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataKP.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataKP.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataKP.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataKP.page !== dataKP.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataKP.page !== dataKP.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataKP.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- BEGIN MODAL ADD TOPIK -->
    <div id="modal-add-topik-kp" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Tambah Topik</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-add-topik-kp')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col mb-2">
            <p class="text-base font-medium mb-1">Topik</p>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Topik"
              v-model="dataKP.exDataTopik.topik"
            />
          </div>
          <div class="flex flex-col mb-2">
            <p class="text-base font-medium mb-1">Kategori</p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="dataKP.exDataTopik.kategori"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(k, idx) in dataKP.listKategori"
                  v-bind:key="idx"
                  :value="k.id"
                >
                  {{ k.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <p class="text-base font-medium mb-1">Uraian Pertanyaan</p>
          <div class="preview">
            <CKEditor
              v-model="dataKP.exDataTopik.content"
              :editor="classicEditor"
              :config="editorConfig"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-add-topik-kp')"
          >
            Close
          </button>
          <button
            type="button"
            class="button whitespace-nowrap w-auto bg-theme-1 text-white"
            @click="tambahTopik();"
          >
            Tambah Topik
          </button>
        </div>
      </div>
    </div>
    <!-- END MODAL ADD TOPIK -->

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";

import ClassicEditor from "@ckeditor/ckeditor5-editor-classic/src/classiceditor";
import InlineEditor from "@ckeditor/ckeditor5-editor-inline/src/inlineeditor";
import BalloonEditor from "@ckeditor/ckeditor5-editor-balloon/src/ballooneditor";
import DocumentEditor from "@ckeditor/ckeditor5-editor-decoupled/src/decouplededitor";
import EssentialsPlugin from "@ckeditor/ckeditor5-essentials/src/essentials";
import BoldPlugin from "@ckeditor/ckeditor5-basic-styles/src/bold";
import ItalicPlugin from "@ckeditor/ckeditor5-basic-styles/src/italic";
import UnderlinePlugin from "@ckeditor/ckeditor5-basic-styles/src/underline";
import StrikethroughPlugin from "@ckeditor/ckeditor5-basic-styles/src/strikethrough";
import CodePlugin from "@ckeditor/ckeditor5-basic-styles/src/code";
import SubscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/subscript";
import SuperscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/superscript";
import LinkPlugin from "@ckeditor/ckeditor5-link/src/link";
import ParagraphPlugin from "@ckeditor/ckeditor5-paragraph/src/paragraph";
import EasyImage from "@ckeditor/ckeditor5-easy-image/src/easyimage";
import Font from "@ckeditor/ckeditor5-font/src/font";
import Heading from "@ckeditor/ckeditor5-heading/src/heading";
import HeadingButtonsUI from "@ckeditor/ckeditor5-heading/src/headingbuttonsui";
import Highlight from "@ckeditor/ckeditor5-highlight/src/highlight";

import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const config = api.configGet();

    const dataKP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      exDataTopik: {
        topik: "",
        kategori: "",
        content: "",
      },
      listKategori: [],
    });

    const titleTable: Array<string> = ["Topik", "Kategori", "Tanggal", "#"];

    const getData = () => {
      cash("#loadingKP").show();

      const page = dataKP.page;
      const pageSize = dataKP.pageSize;
      const search = dataKP.search;
      axios
        .get(
          api.memberKelasPrivate +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get kp ", rs);

          dataKP.data = rs;
          dataKP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataKP.paginate ", dataKP.paginate);
          cash("#loadingKP").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingKP").hide();
          // console.log("response get kp err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const getKategoriKelas = () => {
      gpf.gLoading.show();

      axios
        .get(
          api.memberKelasKategori, config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get kp ", rs);

          dataKP.listKategori = rs;
          gpf.gLoading.hide();
          gpf.showModal('#modal-add-topik-kp');
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get kp err ", error.response);
          await gpf.handleEds(error);
        });
    };

    const tambahTopik = () => {
      gpf.gLoading.show();

      const ex = dataKP.exDataTopik;
      const params = new URLSearchParams();
      params.append("title", ex.topik);
      params.append("idKategori", ex.kategori);
      params.append("content", ex.content);

      axios
        .post(
          api.memberKelasPrivate, params, config
        )
        .then(async (result: any) => {
          gpf.gLoading.hide();

          gpf.hideModal('#modal-add-topik-kp');
          await gpf.handleSds(result);
          getData();

          ex.topik = "";
          ex.kategori = "";
          ex.content = "";
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response post kp err ", error.response);
          await gpf.handleEds(error);
        });
    };

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataKP.page = p;
      getData();
    };

    const nextPage = () => {
      dataKP.page = dataKP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataKP.page = dataKP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataKP.page = dataKP.paginate.totalPages;
      getData();
    };

    // BEGIN for CKEditor
    const date = ref("");
    const classicEditor = ClassicEditor;
    const inlineEditor = InlineEditor;
    const balloonEditor = BalloonEditor;
    const documentEditor = DocumentEditor;
    const simpleEditorConfig = {
      plugins: [
        ParagraphPlugin,
        BoldPlugin,
        UnderlinePlugin,
        ItalicPlugin,
        LinkPlugin,
      ],
      toolbar: {
        items: ["bold", "italic", "underline", "link"],
      },
    };
    const editorConfig = {
      cloudServices: {
        tokenUrl: "",
        uploadUrl: "",
      },
      plugins: [
        Font,
        EssentialsPlugin,
        BoldPlugin,
        UnderlinePlugin,
        StrikethroughPlugin,
        ItalicPlugin,
        LinkPlugin,
        ParagraphPlugin,
        CodePlugin,
        SubscriptPlugin,
        SuperscriptPlugin,
        EasyImage,
        Heading,
        HeadingButtonsUI,
        Highlight,
      ],
      toolbar: {
        items: [
          "fontSize",
          "fontFamily",
          "fontColor",
          "fontBackgroundColor",
          "bold",
          "italic",
          "underline",
          "strikethrough",
          "code",
          "subscript",
          "superscript",
          "link",
          "undo",
          "redo",
          "imageUpload",
          "highlight",
        ],
      },
    };
    const initDocumentEditor = (editor: any) => {
      const toolbarContainer = document.querySelector(
        ".document-editor__toolbar"
      );
      toolbarContainer?.appendChild(editor.ui.view.toolbar.element);
      const win: any = window;
      win.editor = editor;
    };
    // END for CKEditor

    return {
      salesReportFilter,
      titleTable,
      dataKP,
      getData,
      router,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      getKategoriKelas,
      tambahTopik,

      // for CKEditor
      date,
      classicEditor,
      inlineEditor,
      balloonEditor,
      documentEditor,
      simpleEditorConfig,
      editorConfig,
      initDocumentEditor,
    };
  },
  data() {
    return {};
  },
  methods: {
    detailKP(data: any) {
      this.$router.push('kelas-private/' + data.uuid)
    }
  },
});
</script>
