<template>
  <div class="grid grid-cols-12 gap-3">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-3">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">Dashboard Member</h2>
          <a href="javascript:;" class="ml-auto flex text-theme-1 dark:text-theme-10">
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> Reload Data
          </a>
        </div>
        <div class="grid grid-cols-12 gap-3 mt-3">
          <div
            v-for="(k, idx) in listDM.listTMember"
            :key="idx"
            class="col-span-12 sm:col-span-6 xl:col-span-3 intro-y mb-3"
          >
            <UrlSlug v-if="k.is_internal_link" :to="'/' + role + k.url" >
              <div class="report-box zoom-in">
                <div class="box p-5">
                  <div class="flex  items-center">
                    <p class="text-base font-medium mr-2">{{ k.title }}</p>
                    <div class="ml-auto">
                      <component :is="k.icon" class="w-10 h-10 text-primary" />
                    </div>
                  </div>
                </div>
              </div>
            </UrlSlug>
            <a v-else :href="k.url" :target="k.target">
              <div class="report-box zoom-in">
                <div class="box p-5">
                  <div class="flex  items-center">
                    <p class="text-base font-medium mr-2">{{ k.title }}</p>
                    <div class="ml-auto">
                      <component :is="k.icon" class="w-10 h-10 text-primary" />
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </div>

        </div>
        <div class="grid grid-cols-12 gap-3 mt-3">
          <LoadingDiv id="loadingWTopMember" />
        </div>
        <div class="grid grid-cols-12 gap-2 mt-3">
          <div class="col-span-12 sm:col-span-6 xxl:col-span-6 intro-y mb-1">
            <div class="box pb-3">
              <div class="flex items-center p-5 border-b-2 border-gray-200 dark:border-dark-5">
                <h3 class="text-base font-medium mr-2">Syarat & Ketentuan Garansi</h3>
              </div>
              <div id="ketUji" class="overflow-x-auto box">
                <table class="table">
                  <tbody>
                    <tr
                      v-for="(du, idx) in listDM.listSK"
                      v-bind:key="idx"
                      :class="[( idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '')]"
                    >
                      <td class="border-b dark:border-dark-5 w-8 px-0">{{ idx+1 }}</td>
                      <td class="border-b dark:border-dark-5 px-0" v-html="du.value" ></td>
                    </tr>
                  </tbody>
                </table>
                <LoadingDiv id="loadingSK" />
              </div>
            </div>
          </div>
          <div class="col-span-12 sm:col-span-6 xxl:col-span-6 intro-y mb-1">
            <div class="box pb-3">
              <div class="flex items-center p-5 border-b-2 border-gray-200 dark:border-dark-5">
                <h3 class="text-base font-medium mr-2">Tahapan Belajar Di CPNSonline.Id</h3>
              </div>
              <div id="ketUji" class="overflow-x-auto box">
                <table class="table">
                  <tbody>
                    <tr
                      v-for="(du, idx) in listDM.listTB"
                      v-bind:key="idx"
                      :class="[( idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '')]"
                    >
                      <td class="border-b dark:border-dark-5 w-8 px-0">{{ idx+1 }}</td>
                      <td class="border-b dark:border-dark-5 px-0" v-html="du.value" ></td>
                    </tr>
                  </tbody>
                </table>
                <LoadingDiv id="loadingTB" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide, reactive } from "vue";
import { sk, tahapanBelajar, tryoutPemantapan, topDMember } from "./dashboard-member";
import { VueCookieNext as $ck } from "vue-cookie-next";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };
    const role = api.getRole();
    // console.log("login-data ", $ck.getCookie('login-data'));

    const listDM = reactive({
      listTMember: [],
      listSK: [],
      listTB: [],
    });
    const config = api.configGet();
    // for get wiget top
    axios
      .get(api.memberWidgetTop , config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get wtop ', rs);
        listDM.listTMember = rs;
        cash('#loadingWTopMember').hide();
      })
      .catch(async (error: any) => {
        cash('#loadingWTopMember').hide();
        // console.log("response get wtop err ", error.response);
        await gpf.handleEds(error);
      });

    // for get syarat dan ketentuan garansi
    axios
      .get(api.memberSK , config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get sk ', rs);
        listDM.listSK = rs;
        cash('#loadingSK').hide();
      })
      .catch(async (error: any) => {
        cash('#loadingSK').hide();
        // console.log("response get sk err ", error.response);
        await gpf.handleEds(error);
      });

    // for get data tahapan belajar
    axios
      .get(api.memberTB , config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get tb ', rs);
        listDM.listTB = rs;
        cash('#loadingTB').hide();
      })
      .catch(async (error: any) => {
        cash('#loadingTB').hide();
        // console.log("response get tb err ", error.response);
        await gpf.handleEds(error);
      });

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      role,
      listDM,
    };
  },
  data() {
    return {
      listSK: sk,
      listTB: tahapanBelajar,
      listTP: tryoutPemantapan,
      listTMember: topDMember,
    }
  },
  methods: {
    setCookies(): void {
      $ck.setCookie('user', 'use yeaaah');
      $ck.setCookie('userkuy', 'user kuy yeaaah');
      // console.log('yeaaahhh', $ck.getCookie('userkuy') );
    }
  }
});
</script>
