<template>
  <div class="grid grid-cols-12 gap-6">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">CAT PPPK</h2>
          <a href="" class="ml-auto flex text-theme-1 dark:text-theme-10">
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> Reload Data
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="col-span-12 lg:col-span-12 xxl:col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y grid grid-cols-12 gap-3 sm:gap-6">
        <div
          v-for="(faker, fakerKey) in catPPPK.list"
          :key="fakerKey"
          class="intro-y col-span-6 sm:col-span-4 md:col-span-3 xxl:col-span-2"
        >
          <div
            class="file box rounded-md px-5 pt-5 pb-5 lg:pt-7 sm:px-5 relative zoom-in"
          >
            <a
              href="javascript:;"
              class="w-5/5 lg:w-5/6 file__icon file__icon--image mx-auto"
              @click="saveUrl(faker);"
            >
              <div class="file__icon--image__preview image-fit">
                <img
                  alt="Midone Tailwind HTML Admin Template"
                  :src="'/img/tryout-pppk.jpg'"
                />
              </div>
            </a>
            <a
              href="javascript:;"
              @click="saveUrl(faker);"
              class="block font-medium mt-4 text-center truncate"
            >{{ faker.title }}
            </a>
          </div>
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>
    <LoadingDiv id="loadingCatPppk" />
    <div id="modal-cat-pppk" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">PERHATIAN !!!</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="hM('#modal-cat-pppk');"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <p class="text-base font-medium mb-3">Sebelum mulai mengerjakan Tryout ini pastikan terlebih dahulu :</p>
          <ol class="pl-5" style="list-style: decimal">
            <li>Pastikan Anda menggunakan Perangkat Komputer atau Laptop dengan jaringan Internet yang stabil.</li>
            <li>Waktu Anda sudah siap karena untuk mengerjakan tryout dibutuhkan waktu {{ catPPPK.dataPG.durasi }} menit.</li>
            <li>Pastikan Anda sudah menguasai materi yang telah kami berikan.</li>
            <li>Siapkan alat bantu tulis disamping Anda untuk melakukan catatan-catatan kecil pada saat mulai mengerjakan soal.</li>
            <li>Yakinkan diri Anda untuk bisa berhasil menyelesaikan paket tryout ini dengan bisa menduduki peringkat teratas pada perengkingan.</li>
          </ol>
          <h5 class="text-base font-medium my-3">Nilai Ambang Batas : {{ catPPPK.dataPG.title }} </h5>
          <ol class="pl-5" style="list-style: decimal">
            <li>Passing Grade = {{ catPPPK.dataPG.nilaiPg }}</li>
            <li>Jumlah Soal = {{ catPPPK.dataPG.jmlSoal }}</li>
            <li>Durasi {{ catPPPK.dataPG.durasi }} menit.</li>
          </ol>
          <div v-if="catPPPK.dataPG.jurusan.length !== 0">
            <h5 class="text-base font-medium my-3">Pilih Jurusan : {{ catPPPK.dataPG.title }} </h5>
            <TailSelect
              v-model="catPPPK.selectJurusan"
              :options="{
                classNames: 'w-full',
              }"
            >
              <option
                v-for="(js, idx) in catPPPK.dataPG.jurusan"
                v-bind:key="idx"
                :value="js.kode"
              >
                {{ js.title }}
              </option>
            </TailSelect>
          </div>
          <p class="text-base font-medium text-center mt-3">Pastikan Anda Sudah Berdo'a</p>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="gotoTryoutExam()"
          >
            Mulai
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide, reactive } from "vue";
import { Files, filesPPPK } from "./cat-pppk";
import { useRouter } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  components: {},
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    const router = useRouter();
    let files: Array<Files> = [];
    let dataTPPPK: any;
    const config = api.configGet();
    const catPPPK = reactive({
      list: [],
      dataPG: {
        kode: "",
        title: "",
        nilaiPg: 0,
        jmlSoal: 0,
        durasi: 0,
        jurusan: [],
        selectJurusan: "",
      }
    });

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };

    const saveUrl = (data: any) => {
      gpf.gLoading.show();

      axios
        .get(api.memberPassingGrade + data.kode, config)
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log('response get pg ', rs);
          catPPPK.dataPG = rs;
          if ( gpf.trimText(rs.jurusan) ) {
            catPPPK.dataPG.jurusan = rs.jurusan;
            catPPPK.dataPG.selectJurusan = rs.jurusan[0].kode;
          } else {
            catPPPK.dataPG.jurusan = [];
            catPPPK.dataPG.selectJurusan = "";
          }

          gpf.gLoading.hide();
          gpf.showModal("#modal-cat-pppk");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get pg err ", error.response);
          await gpf.handleEds(error);
        });
    }

    const gotoTryoutExam = () => {
      gpf.hideModal("#modal-cat-pppk");
      gpf.gLoading.show();
      setTimeout(() => {
        if ( catPPPK.dataPG.jurusan.length !== 0 ) {
            router.push('cat-pppk/'+ catPPPK.dataPG.kode + '?jurusan=' + catPPPK.dataPG.selectJurusan);
        } else {
          router.push('cat-pppk/'+ catPPPK.dataPG.kode);
        }
      }, 700);
    }

    const getData = () => {
      files = filesPPPK();
    };
    getData();

    axios
      .get(api.memberJenisTryout + 'pppk', config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get mp ', rs);
        catPPPK.list = rs;
        cash("#loadingCatPppk").hide();
      })
      .catch(async (error: any) => {
        cash("#loadingCatPppk").hide();
        // console.log("response get mp err ", error.response);
        await gpf.handleEds(error);
      });

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      files,
      saveUrl,
      gotoTryoutExam,
      catPPPK,
    };
  },
  methods: {
    hM(id: string) {
      gpf.hideModal(id);
    }
  }
});
</script>
