<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data User</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addUser()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <!-- <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="select"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(skd, idx) in fSKD"
                  v-bind:key="idx"
                  :value="skd.slug"
                >
                  {{ skd.fileName }}
                </option>
              </TailSelect>
            </div> -->
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataUser.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataUser.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <div class="w-12">
                    <img class="w-full" :src="u.photo" alt="photo-user" :title="'Photo ' + u.name">
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.username }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.email }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.phone }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.role }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.createdAt }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailUserApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editUser(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteUserApi(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataUser.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data User.</p>
          </div>
          <LoadingDiv id="loadingDUadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataUser.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataUser.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataUser.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataUser.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataUser.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataUser.page !== dataUser.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataUser.page !== dataUser.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataUser.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
    <!-- Modal Add and Edit User -->
    <div id="modal-data-user" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah User" : "Edit User" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-user')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nama</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Nama Lengkap"
              v-model="exDataUser.name"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Email Aktif"
              v-model="exDataUser.email"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Phone</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="No. HP"
              v-model="exDataUser.phone"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Akses / Role</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataUser.roles"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(r, idx) in listRoles"
                  v-bind:key="idx"
                  :value="r.value"
                >
                  {{ r.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataUser.status"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Username</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Username"
              v-model="exDataUser.username"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Password</label>
            <div class="relative w-full sm:w-5/6">
              <input
                type="password"
                class="input pr-12 w-full border col-span-10"
                placeholder="Password"
                id="passExDataUser"
                v-model="exDataUser.password"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                @click="showPass('passExDataUser')"
              >
                show
              </div>
            </div>
          </div>
          <div class="flex flex-col">
            <div class="flex flex-col sm:flex-row items-center mt-3">
              <label class="w-full sm:w-20 sm:text-right sm:mr-5">Pin</label>
              <div class="relative w-full sm:w-5/6">
                <input
                  type="text"
                  onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) ) || event.keyCode == 32) return false;"
                  :class="['input pr-12 w-full border col-span-10 ', ( required.pin ? 'border-theme-6' : '')]"
                  placeholder="Pin"
                  maxlength="6"
                  id="pinExDataUser"
                  v-model="exDataUser.pin"
                />
                <div
                  class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                >
                  {{ exDataUser.pin.length }} / 6
                </div>
              </div>
            </div>
            <div v-if="required.pin" class="text-theme-6 my-1 ml-1 sm:ml-24">This field is required</div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-user')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addUserApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editUserApi()"
            v-else
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete User -->
    <div id="delete-modal-data-user" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Nama : "{{ exDeleteUser.name }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-user')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="fixDeleteUser();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail User -->
    <div id="modal-detail-user" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail User</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-user')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nama</label>
            <p class="text-gray-500">{{ detailUser.name }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <p class="text-gray-500">{{ detailUser.email }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Phone</label>
            <p class="text-gray-500">{{ detailUser.phone }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Username</label>
            <p class="text-gray-500">{{ detailUser.username }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Akses</label>
            <p class="text-gray-500">{{ detailUser.role }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <p class="text-gray-500">{{ detailUser.status }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataRoles, dataStatus } from "./data-user";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const config = api.configGet();
    const listRoles = dataRoles();
    const listStatus = dataStatus();

    const titleTable: Array<string> = [
      "Photo",
      "Nama",
      "Username",
      "Email",
      "Phone",
      "Status",
      "Role",
      "Created",
      "#",
    ];

    const dataUser = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingDUadmin").show();

      const page = dataUser.page;
      const pageSize = dataUser.pageSize;
      const search = dataUser.search;
      axios
        .get(
          api.adminDataUser +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataUser.data = rs;
          dataUser.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataUser.paginate ", dataUser.paginate);
          cash("#loadingDUadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDUadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataUser.page = p;
      getData();
    };

    const nextPage = () => {
      dataUser.page = dataUser.page + 1;
      getData();
    };

    const beforePage = () => {
      dataUser.page = dataUser.page - 1;
      getData();
    };

    const lastPage = () => {
      dataUser.page = dataUser.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataUser,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      config,
      listRoles,
      listStatus,
    };
  },
  data() {
    return {
      // for modal data user
      section: "add",

      detailUser: {
        id: 0,
        name: "",
        email: "",
        phone: "",
        photo: "",
        role: "",
        status: "",
        username: "",
      } as any,

      exDataUser: {
        id: 0,
        name: "",
        email: "",
        phone: "",
        status: "1",
        roles: "member",
        username: "",
        pin: "",
        password: "",
      },

      required: {
        name: false,
        email: false,
        phone: false,
        status: false,
        roles: false,
        username: false,
        pin: false,
        password: false,
      },

      exDeleteUser: {
        id: 0,
        name: "",
      }
    };
  },
  methods: {
    async emptyField() {
      const ex = this.exDataUser;
      ex.id = 0;
      ex.name = "";
      ex.email = "";
      ex.phone = "";
      ex.status = "1";
      ex.roles = "member";
      ex.username = "";
      ex.pin = "";
      ex.password = "";
    },
    requiredField(): boolean {
      const ex = this.exDataUser;
      const er = this.required;
      if ( ex.pin ) {
        er.pin = false;
        return true;
      } else {
        er.pin = true;
        return false;
      }
    },
    async addUser() {
      await this.emptyField();
      this.section = "add";
      gpf.showModal("#modal-data-user");
    },
    addUserApi() {
      if ( this.requiredField() ) {
        gpf.gLoading.show();
        const ex = this.exDataUser;

        const body = {
          username: ex.username.trim(),
          password: ex.password ? ex.password : null,
          pin: ex.pin ? ex.pin : null,
          name: ex.name,
          email: ex.email,
          phone: ex.phone,
          status: ex.status,
          roles: ex.roles,
        };

        axios
          .post(api.adminDataUser, body, this.config)
          .then(async (result: any) => {
            gpf.gLoading.hide();

            ex.name = "";
            ex.email = "";
            ex.phone = "";
            ex.roles = "";
            ex.status = "1";
            ex.username = "";

            await gpf.handleSds(result);
            await gpf.hideModal("#modal-data-user");
            await this.emptyField();
            await this.getData();
          })
          .catch(async (error: any) => {
            gpf.gLoading.hide();
            // console.log("response add deu err ", error.response);
            await gpf.handleEds(error);
          });

      }
    },
    async editUser(data: any) {
      await this.emptyField();
      this.section = "edit";

      gpf.gLoading.show();

      axios
        .get(api.adminDataUser + data.id, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          // console.log("rs.status.toLowerCase() ", rs.status.toLowerCase() , rs.status.toLowerCase() === "active")

          const ex = this.exDataUser;
          ex.id = rs.id;
          ex.name = rs.name;
          ex.email = rs.email;
          ex.phone = rs.phone;
          ex.roles = rs.role;
          rs.status.toLowerCase() === "active" ? ex.status = "1" : ex.status = "0";
          ex.username = rs.username;

          await gpf.showModal("#modal-data-user");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editUserApi(): void {
      if ( this.requiredField() ) {

        gpf.gLoading.show();
        const ex = this.exDataUser;
        const body = {
          username: ex.username,
          password: ex.password ? ex.password : null,
          pin: ex.pin ? ex.pin : null,
          name: ex.name,
          email: ex.email,
          phone: ex.phone,
          status: ex.status,
          roles: [ex.roles],
        };
  
        axios
          .put(api.adminDataUser + ex.id, body, this.config)
          .then(async (result: any) => {
            gpf.gLoading.hide();
  
            ex.name = "";
            ex.email = "";
            ex.phone = "";
            ex.roles = "";
            ex.status = "1";
            ex.username = "";
  
            await gpf.handleSds(result);
            await gpf.hideModal("#modal-data-user");
            await this.emptyField();
            await this.getData();
          })
          .catch(async (error: any) => {
            gpf.gLoading.hide();
            // console.log("response get deu err ", error.response);
            await gpf.handleEds(error);
          });
      }

    },
    detailUserApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminDataUser + data.id, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          this.detailUser = rs;
          await gpf.showModal("#modal-detail-user");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    showPass(id: string) {
      const idHtml = cash("#"+ id);
      if (idHtml.attr("type") === "password") {
        idHtml.attr("type", "text");
      } else {
        idHtml.attr("type", "password");
      }
    },
    deleteUserApi(data: any): void {
      const ex = this.exDeleteUser;
      ex.id = data.id;
      ex.name = data.name;

      gpf.showModal("#delete-modal-data-user");
    },
    fixDeleteUser() {
      const ex = this.exDeleteUser;

      gpf.gLoading.show();

      axios
        .delete(api.adminDataUser + ex.id, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.name = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-user");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    }
  },
});
</script>
