<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex sm:flex-row items-center h-auto sm:h-10">
          <h2 class="text-lg font-medium truncate">Form komplain</h2>
          <a href="" class="ml-auto mt-2 sm:mt-0 flex text-theme-1 dark:text-theme-10">
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> Reload Data
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div class="flex md:flex-row items-center col-span-3 md:col-span-2">
            <button
              @click="gpf.showModal('#modal-add-topik-kp');"
              class="button flex flex-row items-center bg-theme-1 hover:bg-blue-700 text-white w-auto pl-0 shadow-md mb-2 md:mb-0"
            >
              <PlusIcon class="w-6 h-6 mx-2 font-bold" />
              <span class="inline-block">Topik Baru</span>
            </button>
          </div>
          <div class="w-full col-span-3 md:col-span-1">
            <div class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto">
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >{{ k }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in paginate.dataPaginate"
                v-bind:key="idx"
                :class="[
                  ( idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '')
                ]"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">{{ u.peringkat }}</td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">{{ u.nomorTes }}</td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">{{ u.namaPeserta }}</td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">{{ u.skorTWK }}</td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">{{ u.skorTIU }}</td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <router-link
                    :to="'/form-komplain/pwd'"
                  >
                    <Tippy tag="span" class="button inline-block text-white" content="View Respon">
                      <EyeIcon class="text-primary" />
                    </Tippy>
                  </router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li>
            <a class="pagination__link" href="javascript:;" @click="firstPage(paginate.startIndex)">
              <ChevronsLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="javascript:;" @click="backPage()">
              <ChevronLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li
            v-for="(ku, idx) in paginate.pages"
            v-bind:key="idx"

          >
            <a
              :class="[
                'pagination__link',
                (ku === paginate.currentPage ? 'pagination__link--active' : '' ),
              ]"
              href="#"
            >
            {{ ku }}
            </a>
          </li>
          <li>
            <a class="pagination__link" href="javascript:;" @click="nextPage()">
              <ChevronRightIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="javascript:;" @click="lastPage(paginate.endIndex)">
              <ChevronsRightIcon class="w-4 h-4" />
            </a>
          </li>
        </ul>
        <select class="w-20 input box mt-3 sm:mt-0">
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- BEGIN MODAL ADD TOPIK -->
    <div id="modal-add-topik-kp" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Tambah Topik</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="gpf.hideModal('#modal-add-topik-kp');"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="mb-3">
            <label class="text-base">Topik</label>
            <input type="text" class="input w-full border mt-1" placeholder="Topik Pertanyaan" />
          </div>
          <div class="mb-3">
            <label class="text-base">Uraian Pertanyaan</label>
            <div class="preview mb-t">
              <CKEditor
                v-model="editorData"
                :editor="classicEditor"
                :config="editorConfig"
              />
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="gpf.hideModal('#modal-add-topik-kp');"
          >
            Close
          </button>
          <button
            type="button"
            class="button whitespace-nowrap w-auto bg-theme-1 text-white"
          >
            Save Changes
          </button>
        </div>
      </div>
    </div>
    <!-- END MODAL ADD TOPIK -->
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide } from "vue";
import { Files, pplist, filesPPPK, filesSKD, setDataList } from "./form-komplain";
import { useRouter } from "vue-router";
import { helper as $h } from "@/utils/helper";
import { gpf } from "@/utils/global-functions";

import ClassicEditor from "@ckeditor/ckeditor5-editor-classic/src/classiceditor";
import InlineEditor from "@ckeditor/ckeditor5-editor-inline/src/inlineeditor";
import BalloonEditor from "@ckeditor/ckeditor5-editor-balloon/src/ballooneditor";
import DocumentEditor from "@ckeditor/ckeditor5-editor-decoupled/src/decouplededitor";
import EssentialsPlugin from "@ckeditor/ckeditor5-essentials/src/essentials";
import BoldPlugin from "@ckeditor/ckeditor5-basic-styles/src/bold";
import ItalicPlugin from "@ckeditor/ckeditor5-basic-styles/src/italic";
import UnderlinePlugin from "@ckeditor/ckeditor5-basic-styles/src/underline";
import StrikethroughPlugin from "@ckeditor/ckeditor5-basic-styles/src/strikethrough";
import CodePlugin from "@ckeditor/ckeditor5-basic-styles/src/code";
import SubscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/subscript";
import SuperscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/superscript";
import LinkPlugin from "@ckeditor/ckeditor5-link/src/link";
import ParagraphPlugin from "@ckeditor/ckeditor5-paragraph/src/paragraph";
import EasyImage from "@ckeditor/ckeditor5-easy-image/src/easyimage";
import Font from "@ckeditor/ckeditor5-font/src/font";
import Heading from "@ckeditor/ckeditor5-heading/src/heading";
import HeadingButtonsUI from "@ckeditor/ckeditor5-heading/src/headingbuttonsui";
import Highlight from "@ckeditor/ckeditor5-highlight/src/highlight";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    const router = useRouter();
    let files: Array<Files> = [];
    let fSKD: Array<Files> = [];
    let dataTPPPK: any;

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };

    const saveUrl = (data: any) => {
      dataTPPPK = data;
      // console.log('dataTPPPK', dataTPPPK);
      gpf.showModal("#modal-add-topik-kp");
    }
    const gotoTryoutExam = () => {
      // console.log('gotoTryoutExam ', dataTPPPK);
      gpf.hideModal("#modal-add-topik-kp");
      router.push({name: 'add-topik-kp-exam', params: dataTPPPK});
    }

    const getData = () => {
      files = filesPPPK();
      fSKD = filesSKD();
    };
    getData();

    const titleTable: Array<string> = [
      "No",
      "Tanggal",
      "Topik",
      "Status",
      "Update",
      "#"
    ];

    // BEGIN for CKEditor
    const date = ref("");
    const classicEditor = ClassicEditor;
    const inlineEditor = InlineEditor;
    const balloonEditor = BalloonEditor;
    const documentEditor = DocumentEditor;
    const simpleEditorConfig = {
      plugins: [
        ParagraphPlugin,
        BoldPlugin,
        UnderlinePlugin,
        ItalicPlugin,
        LinkPlugin,
      ],
      toolbar: {
        items: ["bold", "italic", "underline", "link"],
      },
    };
    const editorConfig = {
      cloudServices: {
        tokenUrl: "",
        uploadUrl: "",
      },
      plugins: [
        Font,
        EssentialsPlugin,
        BoldPlugin,
        UnderlinePlugin,
        StrikethroughPlugin,
        ItalicPlugin,
        LinkPlugin,
        ParagraphPlugin,
        CodePlugin,
        SubscriptPlugin,
        SuperscriptPlugin,
        EasyImage,
        Heading,
        HeadingButtonsUI,
        Highlight,
      ],
      toolbar: {
        items: [
          "fontSize",
          "fontFamily",
          "fontColor",
          "fontBackgroundColor",
          "bold",
          "italic",
          "underline",
          "strikethrough",
          "code",
          "subscript",
          "superscript",
          "link",
          "undo",
          "redo",
          "imageUpload",
          "highlight",
        ],
      },
    };
    const editorData = ref("<p>Content of the editor.</p>");
    const initDocumentEditor = (editor: any) => {
      const toolbarContainer = document.querySelector(
        ".document-editor__toolbar"
      );
      toolbarContainer?.appendChild(editor.ui.view.toolbar.element);
      const win: any = window;
      win.editor = editor;
    };
    // END for CKEditor

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      files,
      saveUrl,
      gotoTryoutExam,
      fSKD,
      titleTable,

      // for CKEditor
      date,
      classicEditor,
      inlineEditor,
      balloonEditor,
      documentEditor,
      simpleEditorConfig,
      editorConfig,
      editorData,
      initDocumentEditor,
    };
  },
  data() {
    return {
      select: "paket-skd-1",
      dataList: [] as pplist[],

      // pagination
      minIndex: 0,
      maxPerPage: 5,
      maxIndex: 4,
      activePage: 0,
      numberOfPages: [],
      goToPageInput: 0,
      sortedData: [],
      searchActive: 0,
      formData: {},

      // another pagination
      paginate: {},
      currentPage: 1,
      pageSize: 10,
      maxPages: 3,
    }
  },
  methods: {
    init() {
      this.dataList = setDataList();
      // console.log('this.dataList ', this.dataList);
      this.getDataPaginate();
    },
    // START FUNCTION-PAGINATION-TABLE
    pagesCounter(itemLength: number) {
      let m = Math.ceil(itemLength / this.maxPerPage);
      if (m <= 1) {
        m = 1;
      }
      const nop: any = Array(m)
        .fill(0, 0)
        .map((x, i) => i);
      this.numberOfPages = nop;
    },
    openPage(selectedPage: number) {
      const page = selectedPage + 1;
      this.maxIndex = page * this.maxPerPage - 1;
      this.minIndex = page * this.maxPerPage - this.maxPerPage;
      this.activePage = selectedPage;
    },
    changeMaxPerPage(e: number) {
      const page = 1;
      this.maxPerPage = e;
      this.pagesCounter(this.dataList.length);
      this.maxIndex = page * this.maxPerPage - 1;
      this.minIndex = page * this.maxPerPage - this.maxPerPage;
      this.activePage = 0;
    },
    goToPage() {
      const page = this.goToPageInput - 1;
      if (
        typeof page === 'number' &&
        page <= this.numberOfPages.length - 1 &&
        page >= 0
      ) {
        this.openPage(page);
      }
    },
    // END FUNCTION-PAGINATION-TABLE

    getData(currentPage:number, pageSize:number, maxPages:number) {
      return $h.paginationLocal(this.dataList.length, currentPage, pageSize, maxPages, this.dataList);
    },
    getDataPaginate() {
      const dddlist = this.getData(this.currentPage, this.pageSize, this.maxPages);
      this.paginate = dddlist;
    },
    nextPage() {
      this.currentPage++;
      const dddlist = this.getData(this.currentPage, this.pageSize, this.maxPages);
      this.paginate = dddlist;
    },
    backPage() {
      this.currentPage--;
      const dddlist = this.getData(this.currentPage, this.pageSize, this.maxPages);
      this.paginate = dddlist;
    },
    lastPage(endIndex: number) {
      const dddlist = this.getData(endIndex + 1, this.pageSize, this.maxPages);
      this.paginate = dddlist;
    },
    firstPage(startIndex: number) {
      const dddlist = this.getData(startIndex + 1, this.pageSize, this.maxPages);
      this.paginate = dddlist;
    }
  },
  mounted() {
    // console.log('paginate data ', $h.paginate(setDataList().length, 1, 10, 3, setDataList()));
    this.init();
  }
});
</script>
