<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex sm:flex-row items-center h-auto sm:h-10">
          <div class="flex flex-col max-w-9/12">
            <h2 class="text-lg font-medium truncate" :title="dataRC.detail.title">
              {{ dataRC.detail.title }}
            </h2>
            <div class="mt-1 text-xs text-gray-600">{{ dataRC.detail.tanggal }}</div>
          </div>
          <div
            class="ml-auto mt-2 sm:mt-0 flex"
          >
						<button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <div class="box p-3">
        <div class="flex flex-col pb-3 mb-1 border-b border-gray-400" >
          <p class="font-bold text-base mb-1">Uraian Pertanyaan :</p>
          <div class="text-sm" v-html="dataRC.detail.content"></div>
        </div>

        <!-- BEGIN CHAT -->
        <p class="font-bold text-base mb-2">Response :</p>
        <div
          class="overflow-y-scroll scrollbar-hidden px-0 md:px-5 pt-5 flex-1"
        >
          <template
            v-for="(a, idx) in dataRC.detail.answers"
            :key="idx"
          >
            <div
              v-if="a.isMentor"
              class="chat__box__text-box flex items-end float-left mb-4"
            >
              <div
                class="w-10 h-10 hidden sm:block flex-none image-fit relative mr-5"
              >
                <img
                  alt="Midone Tailwind HTML Admin Template"
                  class="rounded-full"
                  :src="require(`@/assets/images/cpnsonline-new-logo.png`)"
                />
              </div>
              <div
                class="bg-gray-200 dark:bg-dark-5 px-4 py-3 text-gray-700 dark:text-gray-300 rounded-r-md rounded-t-md"
              >
                <p class="text-base text-left font-medium">Mentor</p>
                <div class="my-2 border-b border-green-500"></div>
                <div v-html="a.content" ></div>
                <div class="mt-1 text-xs text-gray-600">{{ a.tanggal }}</div>
              </div>
            </div>
            <div
              v-else
              class="chat__box__text-box flex items-end float-right mb-4"
            >
              <div
                class="bg-theme-1 px-4 py-3 text-white rounded-l-md rounded-t-md"
              >
                <p class="text-base text-left font-medium">You</p>
                <div class="my-2 border-b border-green-500"></div>
                <div v-html="a.content" ></div>
                <div class="mt-1 text-xs text-theme-25">{{ a.tanggal }}</div>
              </div>
              <div
                class="w-10 h-10 hidden sm:block flex-none image-fit relative ml-5"
              >
                <img
                  alt="Midone Tailwind HTML Admin Template"
                  class="rounded-full"
                  :src="photoUser"
                />
              </div>
            </div>
            <div class="clear-both"></div>
          </template>
          <div
            v-if="dataRC.detail.answers.length === 0"
            class="text-base font-bold mb-3 text-center"
          >Belum ada Response</div>
        </div>
        <!-- END CHAT -->

        <div
          class="flex flex-col items-center justify-center border-t border-gray-400 py-5 mt-3"
        >
          <p class="text-base text-center font-medium mb-2">Masukan Balasan</p>
          <div class="preview w-full md:w-3/5 mx-auto">
            <CKEditor
              v-model="dataRC.contentResponse"
              :editor="classicEditor"
              :config="editorConfig"
            />
          </div>
          <button
            type="button"
            class="button whitespace-nowrap w-auto bg-theme-1 text-white mt-3"
            @click="tambahResponse()"
          >
            Simpan Balasan
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRoute } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

import ClassicEditor from "@ckeditor/ckeditor5-editor-classic/src/classiceditor";
import InlineEditor from "@ckeditor/ckeditor5-editor-inline/src/inlineeditor";
import BalloonEditor from "@ckeditor/ckeditor5-editor-balloon/src/ballooneditor";
import DocumentEditor from "@ckeditor/ckeditor5-editor-decoupled/src/decouplededitor";
import EssentialsPlugin from "@ckeditor/ckeditor5-essentials/src/essentials";
import BoldPlugin from "@ckeditor/ckeditor5-basic-styles/src/bold";
import ItalicPlugin from "@ckeditor/ckeditor5-basic-styles/src/italic";
import UnderlinePlugin from "@ckeditor/ckeditor5-basic-styles/src/underline";
import StrikethroughPlugin from "@ckeditor/ckeditor5-basic-styles/src/strikethrough";
import CodePlugin from "@ckeditor/ckeditor5-basic-styles/src/code";
import SubscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/subscript";
import SuperscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/superscript";
import LinkPlugin from "@ckeditor/ckeditor5-link/src/link";
import ParagraphPlugin from "@ckeditor/ckeditor5-paragraph/src/paragraph";
import EasyImage from "@ckeditor/ckeditor5-easy-image/src/easyimage";
import Font from "@ckeditor/ckeditor5-font/src/font";
import Heading from "@ckeditor/ckeditor5-heading/src/heading";
import HeadingButtonsUI from "@ckeditor/ckeditor5-heading/src/headingbuttonsui";
import Highlight from "@ckeditor/ckeditor5-highlight/src/highlight";

export default defineComponent({
  setup() {
    const route = useRoute();
    const slug: any = route.params.slug;
    const config = api.configGet();
    const ld = api.getLoginData();
    const photoUser = ld.photo;

    const dataRC = reactive({
      detail: {
        id: "",
        title: "",
        content: "",
        tanggal: "",
        kategori: "",
        answers: [
          // {
          //   isMentor: true,
          //   tanggal: "2021-02-25 00:00:00",
          //   content: "test reply",
          // },
        ],
      },
      contentResponse: "",
    });

    const getData = async () => {
      gpf.gLoading.show();

      axios
        .get(
          api.memberKelasPrivate + slug,
          config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deukp ", rs);

          dataRC.detail = rs;
          gpf.gLoading.hide();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deukp err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const tambahResponse = () => {
      gpf.gLoading.show();

      const idClass = dataRC.detail.id;
      const content = dataRC.contentResponse;
      const params = new URLSearchParams();
      params.append("idClass", idClass);
      params.append("content", content);

      axios
        .put(
          api.memberKelasPrivate, params, config
        )
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response put rc ", rs);
          dataRC.contentResponse = "";

          await gpf.handleSds(result);
          await getData();

        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response put rc err ", error.response);
          await gpf.handleEds(error);
        });
    };

    // BEGIN for CKEditor
    const date = ref("");
    const classicEditor = ClassicEditor;
    const inlineEditor = InlineEditor;
    const balloonEditor = BalloonEditor;
    const documentEditor = DocumentEditor;
    const simpleEditorConfig = {
      plugins: [
        ParagraphPlugin,
        BoldPlugin,
        UnderlinePlugin,
        ItalicPlugin,
        LinkPlugin,
      ],
      toolbar: {
        items: ["bold", "italic", "underline", "link"],
      },
    };
    const editorConfig = {
      cloudServices: {
        tokenUrl: "",
        uploadUrl: "",
      },
      plugins: [
        Font,
        EssentialsPlugin,
        BoldPlugin,
        UnderlinePlugin,
        StrikethroughPlugin,
        ItalicPlugin,
        LinkPlugin,
        ParagraphPlugin,
        CodePlugin,
        SubscriptPlugin,
        SuperscriptPlugin,
        EasyImage,
        Heading,
        HeadingButtonsUI,
        Highlight,
      ],
      toolbar: {
        items: [
          "fontSize",
          "fontFamily",
          "fontColor",
          "fontBackgroundColor",
          "bold",
          "italic",
          "underline",
          "strikethrough",
          "code",
          "subscript",
          "superscript",
          "link",
          "undo",
          "redo",
          "imageUpload",
          "highlight",
        ],
      },
    };
    const editorData = ref("<p>Content of the editor.</p>");
    const initDocumentEditor = (editor: any) => {
      const toolbarContainer = document.querySelector(
        ".document-editor__toolbar"
      );
      toolbarContainer?.appendChild(editor.ui.view.toolbar.element);
      const win: any = window;
      win.editor = editor;
    };
    // END for CKEditor

    return {
      dataRC,
      getData,
      tambahResponse,
      photoUser,

      // for CKEditor
      date,
      classicEditor,
      inlineEditor,
      balloonEditor,
      documentEditor,
      simpleEditorConfig,
      editorConfig,
      editorData,
      initDocumentEditor,
    };
  },
  data() {
    return {};
  },
  methods: {},
});
</script>
