<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex sm:flex-row items-center h-auto sm:h-10">
          <h2 class="text-lg font-medium truncate">Kelas Webinar</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addKW()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div class="w-full col-span-3">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataKW.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataKW.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <div class="w-36">
                    <img
                      class="w-full"
                      :src="u.banner"
                      :alt="'Banner Webinar'"
                    />
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.startedAt }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 min-w-48">{{ u.description }}</td>
                <td class="border-b dark:border-dark-5 max-w-72 truncate">
                  <a :href="u.link" target="_blank">
                    {{ u.link }}
                  </a>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
								<td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailKWApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editKW(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteKW(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataKW.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Kelas Webinar.</p>
          </div>
          <LoadingDiv id="loadingKW" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataKW.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataKW.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataKW.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataKW.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataKW.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataKW.page !== dataKW.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataKW.page !== dataKW.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataKW.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

		<!-- Modal Add and Edit Kelas Webinar -->
    <div id="modal-data-kw" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Kelas Webinar" : "Edit Kelas Webinar" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-kw')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center ">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title Kelas Webinar"
              v-model="exDataKW.title"
            />
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Description</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Description"
              v-model="exDataKW.description"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Banner</label>
            <div class="flex flex-col w-10/12">
              <div class="flex items-center mb-2">
                <input
                  type="file"
                  class="button w-10 border flex-1 hidden"
                  placeholder="Banner"
                  id="fileBannerDM"
                  v-on:change="handleFile($event)"
                />
                <button
                  v-if="exDataKW.banner === ''"
                  @click="chooseBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
                >
                  Choose File
                </button>
                <button
                  v-else
                  @click="removeBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-32 whitespace-nowrap"
                >
                  Remove Banner
                </button>
                <span class="text-sm ml-2 truncate">{{ exDataKW.nameBanner }}</span>
              </div>
              <span class="text-sm">Maks Ukuran File Banner = 1MB.</span>
              <span
                v-if="exDataKW.oversizeBanner"
                class="text-sm text-red-700"
              >Ukuran Banner melebihi 1MB, Silahkan Pilih Banner kembali.</span>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Preview Image</label>
            <div class="show-image w-80">
              <img
                v-if="section === 'add'"
                id="showImage"
                src
                alt
              />
              <img
                v-if="section === 'edit'"
                id="showImage"
                :src="exDataKW.banner"
                alt
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Link</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Link"
              v-model="exDataKW.link"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <div class="min-w-32 flex flex-row">
              <TailSelect
                v-model="exDataKW.isActive"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(f, idx) in listStatus"
                  v-bind:key="idx"
                  :value="f.value"
                >
                  {{ f.title }}
                </option>
              </TailSelect>
            </div>
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Tanggal Mulai</label>
            <div class="relative w-5/6">
              <div
                class="absolute rounded-l w-10 h-full flex items-center justify-center bg-gray-100 border text-gray-600 dark:bg-dark-1 dark:border-dark-4"
              >
                <CalendarIcon class="w-4 h-4" />
              </div>
              <Litepicker
                v-model="exDataKW.tglMulai"
                :options="{
                  autoApply: false,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input pl-12 border text-center"
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-28 sm:text-right sm:mr-5">Waktu Mulai</label>
            <div class="flex flex-row items-center w-full sm:w-5/6">
              <div class="flex flex-col justify-center items-center w-3/12">
                <span class="text-sm text-gray-500">Jam</span>
                <TailSelect
                  v-model="exDataKW.jam"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(f, idx) in 25"
                    v-bind:key="idx"
                    :value="idx < 10 ? '0'+idx : idx"
                  >
                    <span v-if="idx < 10" >0{{idx}}</span>
                    <span v-else >{{idx}}</span>
                  </option>
                </TailSelect>
              </div>
              <span class="mx-3 mt-5 font-bold">:</span>
              <div class="flex flex-col justify-center items-center w-3/12">
                <span class="text-sm text-gray-500">Menit</span>
                <TailSelect
                  v-model="exDataKW.menit"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(f, idx) in 60"
                    v-bind:key="idx"
                    :value="idx < 10 ? '0'+idx : idx"
                  >
                    <span v-if="idx < 10" >0{{idx}}</span>
                    <span v-else >{{idx}}</span>
                  </option>
                </TailSelect>
              </div>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-kw')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addKWApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editKWApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

		<!-- Modal Detail Kelas Webinar -->
    <div id="modal-detail-materi" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Kelas Webinar</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-materi')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <p class="text-gray-500">{{ detailKW.title }}</p>
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Description</label>
            <p class="text-gray-500">{{ detailKW.description }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Banner</label>
            <div class="show-image w-80">
              <img
                id="showImageDetail"
                :src="detailKW.banner"
                alt
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Link</label>
            <a :href="detailKW.link" target="_blank" class="text-gray-500 truncate max-w-9/12">{{ detailKW.link }}</a>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <p class="text-gray-500">{{ detailKW.isActive }}</p>
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-2">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Tanggal Mulai</label>
            <p class="text-gray-500">{{ detailKW.tglMulai }}</p>
          </div>
        </div>
      </div>
    </div>

		<!-- Modal Delete Kelas Webinar Admin -->
    <div id="delete-modal-data-kw" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteKW.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-kw')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteKWApi()"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { helper as $h } from "@/utils/helper";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";
import { dataStatus } from "./kelas-webinar-admin";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
		const listStatus = dataStatus();

    const dataKW = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const titleTable: Array<string> = [
      "Banner",
      "Tanggal",
      "Title",
      "Deskripsi",
      "Link",
      "Status",
			"#",
    ];

    const getData = async () => {
      cash("#loadingKW").show();

      const page = dataKW.page;
      const pageSize = dataKW.pageSize;
      const search = dataKW.search;
      axios
        .get(
          api.adminKelasWebinar +
            "?page=" +
            page +
            "&pageSize=" +
            pageSize +
            "&search=" +
            search,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get kw ", rs);

          dataKW.data = rs;
          dataKW.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataKW.paginate ", dataKW.paginate);
          cash("#loadingKW").hide();
        })
        .catch(async (error: any) => {
          // console.log("response get kw err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataKW.page = p;
      getData();
    };

    const nextPage = () => {
      dataKW.page = dataKW.page + 1;
      getData();
    };

    const beforePage = () => {
      dataKW.page = dataKW.page - 1;
      getData();
    };

    const lastPage = () => {
      dataKW.page = dataKW.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      dataKW,
      getData,
      router,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
			configGet,
			listStatus,
    };
  },
  data() {
    return {
			section: "add",
			exDataKW: {
				id: "",
				title: "",
				description: "",
				banner: "",
				nameBanner: "",
				oversizeBanner: false,
				tempBanner: "",
				link: "",
				isActive: "1",
				tglMulai: "",
        jam: "00",
        menit: "00",
			},

			detailKW: {
				title: "",
				description: "",
				banner: "",
				nameBanner: "",
				oversizeBanner: false,
				tempBanner: "",
				link: "",
				isActive: "1",
				tglMulai: "",
			},

			exDeleteKW: {
				id: "",
				title: "",
			},

		};
  },
  methods: {
		chooseBanner() {
      const id = "fileBannerDM";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBanner() {
      const ex = this.exDataKW;
      ex.nameBanner = "Choose File";
      ex.banner = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataKW;

      if (sizeFiles > 1000000) {
        ex.oversizeBanner = true;
        this.removeBanner();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBanner = nameFile;
        ex.banner = files[0];
        ex.oversizeBanner = false;
        // const bodyFormData = new FormData();
        // bodyFormData.append("files", files[0]);

        this.renderImage(target.files[0]);
      }
      // console.log("evt ", evt);
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
		async emptyField() {
			const ex = this.exDataKW;
			const dateNow = new Date();

			ex.title = "";
			ex.description = "";
			ex.banner = "";
			ex.link = "";
			ex.isActive = "1";
			ex.tglMulai = $h.formatDate(dateNow.toString(), "DD MMM, YYYY");
		},
		async addKW() {
      await this.emptyField();
      this.section = "add";
      gpf.showModal("#modal-data-kw");
    },
    addKWApi() {
      gpf.gLoading.show();
      const ex = this.exDataKW;
      const body = new FormData();
      body.append("title", ex.title);
      body.append("description", ex.description);
      body.append("banner", ex.banner);
      body.append("link", ex.link);
      body.append("isActive", ex.isActive);
      body.append("tglMulai", $h.formatDate(`${ex.tglMulai} ${ex.jam}:${ex.menit}:00`, "YYYY-MM-DD HH:mm:ss"));

      axios
        .post(api.adminKelasWebinar, body, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-kw");
          await this.emptyField();
          await this.getData();
        })
        .catch(async(error: any) => {
          gpf.gLoading.hide();
          // console.log("response add dm err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editKW(data: any) {
      this.section = "edit";

      gpf.gLoading.show();

      axios
        .get(api.adminKelasWebinar + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.exDataKW;
          ex.id = rs.id;
          ex.title = rs.title;
          ex.description = rs.description;
          ex.banner = rs.banner;
          ex.nameBanner = rs.banner;
          ex.tempBanner = rs.banner;
          ex.link = rs.link;
          ex.isActive = rs.isActive ? "1" : "0";
					ex.tglMulai = $h.formatDate(rs.startedAt, "DD MMM, YYYY");
          
          const jam = rs.startedAt.split(" ")[1].split(":")[0];
          const menit = rs.startedAt.split(" ")[1].split(":")[1];
          // console.log("jam ", jam);
          ex.jam = jam;
          ex.menit = menit;

          await gpf.showModal("#modal-data-kw");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
					await gpf.handleEds(error);
        });
    },
    editKWApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataKW;
      const body = new FormData();
      body.append("title", ex.title);
			body.append("description", ex.description);
      if ( ex.nameBanner !== ex.tempBanner ) {
        body.append("banner", ex.banner);
      } else {
        body.append("banner", "");
      }
      body.append("link", ex.link);
      body.append("isActive", ex.isActive);
      body.append("tglMulai", $h.formatDate(`${ex.tglMulai} ${ex.jam}:${ex.menit}:00`, "YYYY-MM-DD HH:mm:ss"));

      axios
        .put(api.adminKelasWebinar + ex.id, body, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-kw");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    detailKWApi(data: any): void {
      gpf.gLoading.show();
      const exd = this.detailKW;

      axios
        .get(api.adminKelasWebinar + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);
          exd.title = rs.title;
          exd.description = rs.description;
          exd.banner = rs.banner;
          exd.isActive = rs.isActive ? "Active" : "Suspend";
          exd.link = rs.link;
          exd.tglMulai = $h.formatDate(rs.startedAt, "DD MMM, YYYY HH:mm:ss");

          await gpf.showModal("#modal-detail-materi");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
					await gpf.handleEds(error);
        });
    },

		deleteKW(data: any): void {
      const ex = this.exDeleteKW;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-kw");
    },
    deleteKWApi() {
      const ex = this.exDeleteKW;

      gpf.gLoading.show();

      axios
        .delete(api.adminKelasWebinar + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = "0";
          ex.title = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-kw");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
	},
});
</script>
