
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import { dataStatus } from "./member";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
		const listStatus = dataStatus();

    const titleTable: Array<string> = [
			"Name",
      "Username",
      "Email",
      "Phone Number",
			"Status",
      "Created",
			"#",
    ];

    const dataSM = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingSM").show();

      const page = dataSM.page;
      const pageSize = dataSM.pageSize;
      const search = dataSM.search;
      axios
        .get(
          api.salesMember +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataSM.data = rs;
          dataSM.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataSM.paginate ", dataSM.paginate);
          cash("#loadingSM").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingSM").hide();
          // console.log("response get du err ", error.response);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataSM.page = p;
      getData();
    };

    const nextPage = () => {
      dataSM.page = dataSM.page + 1;
      getData();
    };

    const beforePage = () => {
      dataSM.page = dataSM.page - 1;
      getData();
    };

    const lastPage = () => {
      dataSM.page = dataSM.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataSM,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
			listStatus,
    };
  },
  data() {
    return {
			section: "add",

			exDataSM: {
				id: "",
				nama: "",
				email: "",
				phone: "",
				username: "",
				password: "",
				status: "1",
			},

			detailDataSM: {
				id: "",
				nama: "",
				email: "",
				phone: "",
				username: "",
				photo: "",
			},

		};
  },
  methods: {
		async emptyField() {
			const ex = this.exDataSM;
			ex.id = "0";
			ex.nama = "";
			ex.email = "";
			ex.phone = "";
			ex.username = "";
			ex.password = "";
		},
		ctc(f: string) {
      const ex: any = this.exDataSM;
      ex[f] = gpf.ctc(ex[f]);
    },
		async addSM() {
      await this.emptyField();
      this.section = "add";
			gpf.showModal("#modal-data-oa");
    },
    addSMApi() {
      gpf.gLoading.show();
      const ex = this.exDataSM;
      const params = new URLSearchParams();
      params.append("nama", ex.nama);
      params.append("email", ex.email);
      params.append("phone", ex.phone);
      params.append("username", ex.username);
      params.append("password", ex.password);

      axios
        .post(api.salesMember, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-oa");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editSM(data: any) {
      this.section = "edit";
      gpf.gLoading.show();

      axios
        .get(api.salesMember + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);
          const ex = this.exDataSM;
          ex.id = rs.id;
          ex.nama = rs.name;
          ex.email = rs.email;
          ex.phone = rs.phone;
          ex.username = rs.username;
					ex.status = rs.status === 'Active' ? '1' : '0';

          await gpf.showModal("#modal-data-oa");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editSMApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataSM;
      const params = new URLSearchParams();
      params.append("nama", ex.nama);
      params.append("email", ex.email);
      params.append("phone", ex.phone);
      params.append("username", ex.username);
      params.append("password", ex.password);

      axios
        .put(api.salesMember + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-oa");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailSMApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.salesMember + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.detailDataSM;
          ex.id = rs.id;
          ex.nama = rs.name;
          ex.email = rs.email;
          ex.phone = rs.phone;
          ex.username = rs.username;
          ex.photo = rs.photo;

          await gpf.showModal("#modal-detail-oa");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },

  },
});
