<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Akun Bank</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
						<button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addAB()"
              v-if="role !== 'member'"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataAB.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  :class="['border-b-2 dark:border-dark-5 whitespace-nowrap', (k === 'member' ? 'hidden' : '')]"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataAB.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.code }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.accountName }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.accountNumber }}
                </td>
								<td 
                  v-if="role !== 'member'"
                  class="border-b dark:border-dark-5 whitespace-nowrap w-20"
                >
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editAB(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteAB(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataAB.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Akun Bank.</p>
          </div>
          <LoadingDiv id="loadingAB" />
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>

		<!-- Modal Add and Edit Akun Bank -->
    <div id="modal-data-ab" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Akun Bank" : "Edit Akun Bank" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-ab')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div 
            class="flex flex-col sm:flex-row items-center"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode Bank</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Kode Bank"
              v-model="exDataAB.code"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nama</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Nama"
              v-model="exDataAB.name"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Account Name</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Account Name"
              v-model="exDataAB.accountName"
            />
          </div>
					<div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Account Number</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Account Number"
              v-model="exDataAB.accountNumber"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-ab')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addABApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editABApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Bidang Soal -->
    <div id="delete-modal-data-ab" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Nama : "{{ exDeleteAB.name }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-ab')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteABApi();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const role = api.getRole();

    const titleTable: Array<string> = [
			"Kode",
      "Nama",
      "Account Name",
      "Account Number",
			"#",
    ];

    // "id": 4,
    // "code": "002",
    // "name": "test Bank BRI",
    // "accountName": "Nasrullah",
    // "accountNumber": "1234567890"

    const dataAB = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingAB").show();

      const page = dataAB.page;
      const pageSize = dataAB.pageSize;
      const search = dataAB.search;
      axios
        .get(
          api.publicAkunBank +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataAB.data = rs;
          dataAB.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataAB.paginate ", dataAB.paginate);
          cash("#loadingAB").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingAB").hide();
          // console.log("response get du err ", error.response);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataAB.page = p;
      getData();
    };

    const nextPage = () => {
      dataAB.page = dataAB.page + 1;
      getData();
    };

    const beforePage = () => {
      dataAB.page = dataAB.page - 1;
      getData();
    };

    const lastPage = () => {
      dataAB.page = dataAB.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataAB,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      role,
    };
  },
  data() {
    return {
			section: "add",

			exDataAB: {
				id: "",
				code: "",
				name: "",
				accountName: "",
				accountNumber: "",
			},

      exDeleteAB: {
        id: "",
        name: "",
      }

		};
  },
  methods: {
		async emptyField() {
			const ex = this.exDataAB;
			ex.id = "0";
			ex.code = "";
			ex.name = "";
			ex.accountName = "";
			ex.accountNumber = "";
		},
		async addAB() {
      await this.emptyField();
      this.section = "add";
			gpf.showModal("#modal-data-ab");
    },
    addABApi() {
      gpf.gLoading.show();
      const ex = this.exDataAB;
      const params = new URLSearchParams();
      params.append("code", ex.code);
      params.append("name", ex.name);
      params.append("accountName", ex.accountName);
      params.append("accountNumber", ex.accountNumber);

      axios
        .post(api.publicAkunBank, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-ab");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editAB(data: any) {
      this.section = "edit";

      const ex = this.exDataAB;
      ex.id = data.id;
      ex.code = data.code;
      ex.name = data.name;
      ex.accountName = data.accountName;
      ex.accountNumber = data.accountNumber;

      gpf.showModal("#modal-data-ab");
    },
    editABApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataAB;
      const params = new URLSearchParams();
      params.append("code", ex.code);
      params.append("name", ex.name);
      params.append("accountName", ex.accountName);
      params.append("accountNumber", ex.accountNumber);

      axios
        .put(api.publicAkunBank + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-ab");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    deleteAB(data: any): void {
      const ex = this.exDeleteAB;
      ex.id = data.id;
      ex.name = data.name;

      gpf.showModal("#delete-modal-data-ab");
    },
    deleteABApi() {
      const ex = this.exDeleteAB;

      gpf.gLoading.show();

      axios
        .delete(api.publicAkunBank + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = "0";
          ex.name = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-ab");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    }

  },
});
</script>
