<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Komisi</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataKomisi.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataKomisi.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.invoice }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.sales }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
								<td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.convertToCurrency(u.amount, 'IDR ') }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.createdAt, "DD MMM, YYYY HH:mm:ss") }}
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataKomisi.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Komisi.</p>
          </div>
          <LoadingDiv id="loadingDMaff" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataKomisi.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataKomisi.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataKomisi.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataKomisi.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataKomisi.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataKomisi.page !== dataKomisi.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataKomisi.page !== dataKomisi.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataKomisi.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();

    const titleTable: Array<string> = [
      "Invoice",
      "Sales",
      "Name",
      "Amount",
      "Created",
    ];

    const dataKomisi = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingDMaff").show();

      const page = dataKomisi.page;
      const pageSize = dataKomisi.pageSize;
      const search = dataKomisi.search;
      axios
        .get(
          api.affKomisi +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataKomisi.data = rs;
          dataKomisi.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataKomisi.paginate ", dataKomisi.paginate);
          cash("#loadingDMaff").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDMaff").hide();
          // console.log("response get du err ", error.response);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataKomisi.page = p;
      getData();
    };

    const nextPage = () => {
      dataKomisi.page = dataKomisi.page + 1;
      getData();
    };

    const beforePage = () => {
      dataKomisi.page = dataKomisi.page - 1;
      getData();
    };

    const lastPage = () => {
      dataKomisi.page = dataKomisi.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataKomisi,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
    };
  },
  data() {
    return {};
  },
  methods: {
  },
});
</script>
