<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Jenis Soal</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addJS()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <!-- <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="select"
                :options="{
                  search: true,
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(skd, idx) in fSKD"
                  v-bind:key="idx"
                  :value="skd.slug"
                >
                  {{ skd.fileName }}
                </option>
              </TailSelect>
            </div> -->
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataJS.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                  v-html="k"
                >
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataJS.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap uppercase">
                  {{ u.paket }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <span class="uppercase">{{ u.kode }}</span> / {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.nilaiPg }} / {{ u.jmlSoal }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.durasi }} menit / <br> {{ u.skorMax }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.sifatSoal === 'umum' ? 'Umum' : 'Bidang' }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.isActive ? "Active" : "Suspend" }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.createdAt }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailJSApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editJS(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteJS(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataJS.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Jenis Soal.</p>
          </div>
          <LoadingDiv id="loadingJSadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>
    <!-- Modal Add and Edit Jenis Soal -->
    <div id="modal-data-js" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Jenis Soal" : "Edit Jenis Soal" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-js')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode</label>
            <div class="relative w-full sm:w-5/6">
              <input
                type="text"
                maxlength="3"
                class="input pr-12 w-full border col-span-10"
                placeholder="Kode"
                v-model="exDataJS.kode"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
              >
                {{ exDataJS.kode.length }} / 3
              </div>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Paket</label>
            <div class="w-full sm:w-1/2">
              <TailSelect
                v-model="exDataJS.paket"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(ss, idx) in listPaket"
                  v-bind:key="idx"
                  :value="ss.value"
                >
                  {{ ss.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title"
              v-model="exDataJS.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nilai PassingGrade</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Nilai PassingGrade"
              v-model="exDataJS.nilaiPg"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jumlah Soal</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Jumlah Soal"
              v-model="exDataJS.jmlSoal"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Durasi</label>
            <div class="relative w-full sm:w-5/6">
              <input
                type="text"
                class="input pr-12 w-full border col-span-10"
                placeholder="Durasi"
                v-model="exDataJS.durasi"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
              >
                Menit
              </div>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Skor Max</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Skor Max"
              v-model="exDataJS.skorMax"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Sifat Soal</label
            >
            <div class="w-full sm:w-1/2">
              <TailSelect
                v-model="exDataJS.sifatSoal"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(ss, idx) in listSifatSoal"
                  v-bind:key="idx"
                  :value="ss.value"
                >
                  {{ ss.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-full sm:w-1/2">
              <TailSelect
                v-model="exDataJS.isActive"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-js')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addJSApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editJSApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Jenis Soal -->
    <div id="delete-modal-data-js" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteJS.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-js')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteJSApi();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail Jenis Soal -->
    <div id="modal-detail-js" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Jenis Soal</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-js')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode</label>
            <p class="text-gray-500">{{ detailJS.kode }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Paket</label>
            <p class="text-gray-500 uppercase">{{ detailJS.paket }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <p class="text-gray-500">{{ detailJS.title }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nilai PassingGrade</label>
            <p class="text-gray-500">{{ detailJS.nilaiPg }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jumlah Soal</label>
            <p class="text-gray-500">{{ detailJS.jmlSoal }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Durasi</label>
            <p class="text-gray-500">{{ detailJS.durasi }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Skor Max</label>
            <p class="text-gray-500">{{ detailJS.skorMax }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Sifat Soal</label>
            <p class="text-gray-500">{{ detailJS.sifatSoal === 'umum' ? "Umum" : "Bidang" }}</p>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Status</label>
            <p class="text-gray-500">{{ detailJS.isActive ? "Active" : "Suspend" }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataPaket, dataSifatSoal, dataStatus } from "./jenis-soal";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listPaket = dataPaket();
    const listSifatSoal = dataSifatSoal();
    const listStatus = dataStatus();

    const titleTable: Array<string> = [
      "Paket",
      "Kode / Title",
      "Nilai PassingGrade / <br> Jumlah Soal",
      "Durasi / <br> Skor Max",
      "Sifat Soal",
      "Status",
      "Created",
      "#",
    ];

    const dataJS = reactive({
      data: [] as any,
      search: "",
    });

    const getData = async () => {
      cash("#loadingJSadmin").show();

      axios
        .get(
          api.adminJenisSoal,
          configGet
        )
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log("response get js ", rs);

          dataJS.data = rs;
          // console.log("dataJS.paginate ", dataJS.paginate);
          cash("#loadingJSadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingJSadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataJS,
      configGet,
      configXwfu,
      listPaket,
      listSifatSoal,
      listStatus,
    };
  },
  data() {
    return {
      // for modal data user
      section: "add",

      detailJS: {
        id: 0,
        kode: "",
        paket: "cpns",
        title: "",
        nilaiPg: "",
        jmlSoal: "",
        durasi: "",
        skorMax: "",
        sifatSoal: "umum",
        isActive: "1",
      } as any,

      exDataJS: {
        id: 0,
        kode: "",
        paket: "cpns",
        title: "",
        nilaiPg: "",
        jmlSoal: "",
        durasi: "",
        skorMax: "",
        sifatSoal: "umum",
        isActive: "1",
      },

      exDeleteJS: {
        id: 0,
        title: "",
      }
    };
  },
  methods: {
    async emptyField() {
      const ex = this.exDataJS;
      ex.kode = "";
      ex.paket = "cpns";
      ex.title = "";
      ex.nilaiPg = "";
      ex.jmlSoal = "";
      ex.durasi = "";
      ex.skorMax = "";
      ex.sifatSoal = "umum";
      ex.isActive = "1";
    },
    async addJS() {
      await this.emptyField();
      this.section = "add";
      gpf.showModal("#modal-data-js");
    },
    addJSApi() {
      gpf.gLoading.show();
      const ex = this.exDataJS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("paket", ex.paket);
      params.append("title", ex.title);
      params.append("nilaiPg", ex.nilaiPg);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("durasi", ex.durasi);
      params.append("skorMax", ex.skorMax);
      params.append("sifatSoal", ex.sifatSoal);
      params.append("isActive", ex.isActive);

      axios
        .post(api.adminJenisSoal, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-js");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    editJS(data: any) {
      this.section = "edit";

      gpf.gLoading.show();

      axios
        .get(api.adminJenisSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          // console.log("rs.status.toLowerCase() ", rs.status.toLowerCase() , rs.status.toLowerCase() === "active")

          const ex = this.exDataJS;
          ex.id = rs.id;
          ex.kode = rs.kode;
          ex.paket = rs.paket;
          ex.title = rs.title;
          ex.nilaiPg = rs.nilaiPg;
          ex.jmlSoal = rs.jmlSoal;
          ex.durasi = rs.durasi;
          ex.skorMax = rs.skorMax;
          ex.sifatSoal = rs.sifatSoal;
          ex.isActive = rs.isActive ? "1" : "0";

          await gpf.showModal("#modal-data-js");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editJSApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataJS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("paket", ex.paket);
      params.append("title", ex.title);
      params.append("nilaiPg", ex.nilaiPg);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("durasi", ex.durasi);
      params.append("skorMax", ex.skorMax);
      params.append("sifatSoal", ex.sifatSoal);
      params.append("isActive", ex.isActive);

      axios
        .put(api.adminJenisSoal + ex.id, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-js");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailJSApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminJenisSoal + data.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          this.detailJS = rs;
          await gpf.showModal("#modal-detail-js");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    deleteJS(data: any): void {
      const ex = this.exDeleteJS;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-js");
    },
    deleteJSApi() {
      const ex = this.exDeleteJS;

      gpf.gLoading.show();

      axios
        .delete(api.adminJenisSoal + ex.id, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.title = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-js");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    }
  },
});
</script>
