<template>
  <div class="grid grid-cols-12 gap-3">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-3">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">
            Pengaturan Affiliate
          </h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->

      <div class="col-span-12 grid grid-cols-12 gap-3 mt-3 intro-y w-full">
        <div class="col-span-12 flex flex-col box p-5">
          <div>
            <label>Kode</label>
            <div class="flex flex-row items-center mt-2">
              <input
                type="text"
                class="input w-full border"
                placeholder="Kode"
                v-model="dataPS.data.kode"
                disabled
              />
              <Tippy
                tag="a"
                href="javascript:;"
                class="inline-block ml-3"
                content="Copy Text"
                @click="$gpf.copy(dataPS.data.kode)"
              >
                <CopyIcon
                  class="w-8 h-8 cursor-pointer text-primary"
                />
              </Tippy>
            </div>
          </div>
          <div class="mt-3">
            <label>Pixel ID</label>
            <div class="flex flex-row items-center mt-2">
              <input
                type="text"
                class="input w-full border"
                placeholder="Pixel ID"
                v-model="dataPS.data.pixelId"
              />
              <Tippy
                tag="a"
                href="javascript:;"
                class="inline-block ml-3"
                content="Copy Text"
                @click="$gpf.copy(dataPS.data.pixelId)"
              >
                <CopyIcon
                  class="w-8 h-8 cursor-pointer text-primary"
                />
              </Tippy>
            </div>
          </div>
          <div class="mt-3">
            <label>Ga ID</label>
            <div class="flex flex-row items-center mt-2">
              <input
                type="text"
                class="input w-full border"
                placeholder="Ga ID"
                v-model="dataPS.data.gaId"
              />
              <Tippy
                tag="a"
                href="javascript:;"
                class="inline-block ml-3"
                content="Copy Text"
                @click="$gpf.copy(dataPS.data.gaId)"
              >
                <CopyIcon
                  class="w-8 h-8 cursor-pointer text-primary"
                />
              </Tippy>
            </div>
          </div>
          <div class="mt-3">
            <label>Tiktok ID</label>
            <div class="flex flex-row items-center mt-2">
              <input
                type="text"
                class="input w-full border"
                placeholder="Tiktok ID"
                v-model="dataPS.data.tiktokId"
              />
              <Tippy
                tag="a"
                href="javascript:;"
                class="inline-block ml-3"
                content="Copy Text"
                @click="$gpf.copy(dataPS.data.tiktokId)"
              >
                <CopyIcon
                  class="w-8 h-8 cursor-pointer text-primary"
                />
              </Tippy>
            </div>
          </div>
          <button 
            @click="updatePSAff()"
            type="button" 
            class="button bg-theme-1 text-white mt-5"
          >
            Update
          </button>
        </div>
      </div>
      <LoadingDiv id="loadingPS" />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";

export default defineComponent({
  setup() {
    const dataPS = reactive({
      data: {
        kode: "",
        pixelId: "",
        gaId: "",
        tiktokId: "",
      },
    });

    const getData = async () => {
      cash("#loadingPS").show();

      const url = api.affPS ;

      const hide = () => cash("#loadingPS").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;

      dataPS.data = rs;
    };
    getData();

    const updatePSAff = async() => {
      gpf.gLoading.show();
      const ex = dataPS.data;
      const params = new URLSearchParams();
      params.append("pixelId", ex.pixelId);
      params.append("gaId", ex.gaId);
      params.append("tiktokId", ex.tiktokId);

      const hitApi = await api.hitApiPost(api.affPS, params);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#modal-data-aw");
      await getData();

      gpf.gLoading.hide();
    };

    return {
      dataPS,
      getData,
      updatePSAff,
    };
  },
  data() {
    return {};
  },
});
</script>
