<template>
  <div class="grid grid-cols-12 gap-3">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-3">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">Dashboard Sales</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="$gpf.showModal('#datepicker-filter-dashboard-affiliate')"
              class="button flex items-center bg-theme-1 text-white mr-2"
            >
              <span class="hidden md:block">Filter</span>
              <FilterIcon class="w-4 h-4 ml-0 md:ml-2" />
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->

      <div class="col-span-12 grid grid-cols-12 gap-3 mt-3 intro-y w-full">
        <div
          v-for="(ds, idx) in dataDB.data"
          :key="idx"
          class="col-span-12 sm:col-span-6 xl:col-span-4 intro-y mb-3"
        >
          <div class="report-box zoom-in">
            <div class="box p-5">
              <div class="flex items-center">
                <p class="text-base font-medium mr-2">
                  {{ ds.title }} : <br />
                  {{ $gpf.convertToCurrency(ds.value, ds.prefix + " ") }}
                </p>
                <div class="ml-auto">
                  <component :is="ds.icon" :class="'w-10 h-10 ' + $gpf.getRT()" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <LoadingDiv id="loadingDS" />
    </div>

    <div id="datepicker-filter-dashboard-affiliate" class="modal">
      <div class="modal__content">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            Filter by Date
          </h2>
        </div>
        <div class="p-5 grid grid-cols-12 gap-4 gap-y-3">
          <div
            class="flex flex-col md:flex-row col-span-12 items-center"
          >
            <Litepicker
              v-model="dataDB.dateRange"
              :options="{
                autoApply: false,
                singleMode: false,
                numberOfColumns: 2,
                numberOfMonths: 2,
                showWeekNumbers: true,
                dropdowns: {
                  minYear: 1990,
                  maxYear: null,
                  months: true,
                  years: true,
                },
              }"
              class="input w-56 border block"
            />
            <button
              @click="getData(); $gpf.hideModal('#datepicker-filter-dashboard-affiliate');"
              class="button bg-theme-1 hover:bg-primary text-white w-24 mt-2 md:mt-0 md:ml-2"
            >
              View
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { helper as $h } from "@/utils/helper";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const dataDB = reactive({
      data: [] as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataDB.dateRange = `${$h.formatDate(
      firstDay.toString(),
      "DD MMM, YYYY"
    )} - ${$h.formatDate(lastDay.toString(), "DD MMM, YYYY")}`;

    const getData = async () => {
      cash("#loadingDS").show();

      const since = $h.formatDate(dataDB.dateRange.split("-")[0], "YYYY-MM-DD");
      const until = $h.formatDate(dataDB.dateRange.split("-")[1], "YYYY-MM-DD");
      const url =
        api.salesWidget +
        "?since=" +
        since +
        "&until=" +
        until;

      const hide = () => cash("#loadingDS").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;

      dataDB.data = rs;

      console.log("getData clicked");
    };
    getData();

    return {
      dataDB,
      getData,
    };
  },
  data() {
    return {};
  },
});
</script>
