
import { defineComponent, ref, provide } from "vue";
import { sk, tahapanBelajar, tryoutPemantapan, listTopDA } from "./dashboard-admin";
import VerticalBarChart from "@/components/vertical-bar-chart/Main.vue";
import LineChart from "@/components/line-chart/Main.vue";

export default defineComponent({
  components: {
    VerticalBarChart,
    LineChart,
  },
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
    };
  },
  data() {
    return {
      listSK: sk,
      listTB: tahapanBelajar,
      listTP: tryoutPemantapan,
      listDA: listTopDA,
    }
  }
});
