<template>
  <div class="grid grid-cols-12 gap-6">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mx-auto md:mr-5 md:ml-0">
            Tutorial Pengguna Web
          </h2>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="col-span-12 lg:col-span-12 xxl:col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y grid grid-cols-12 gap-3 xl:gap-6">
        <div
          v-for="(faker, fakerKey) in videos"
          :key="fakerKey"
          class="intro-y col-span-12 sm:col-span-6 md:col-span-4 xxl:col-span-3"
        >
          <div
            class="file box rounded-md px-5 pt-8 pb-5 px-3 sm:px-5 relative zoom-in"
            @click="setTLS(faker)"
          >
            <iframe
              class="w-full"
              src="https://www.youtube.com/embed/48uS5vzRDaY?rel=0&amp;controls=0&amp;showinfo=0"
              frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen=""
            ></iframe>
            <p class="block font-medium mt-4 text-center truncate">
              {{ faker.fileName }}
            </p>
          </div>
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li>
            <a class="pagination__link" href="">
              <ChevronsLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="">
              <ChevronLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="">...</a>
          </li>
          <li>
            <a class="pagination__link" href="">1</a>
          </li>
          <li>
            <a class="pagination__link pagination__link--active" href="">2</a>
          </li>
          <li>
            <a class="pagination__link" href="">3</a>
          </li>
          <li>
            <a class="pagination__link" href="">...</a>
          </li>
          <li>
            <a class="pagination__link" href="">
              <ChevronRightIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="">
              <ChevronsRightIcon class="w-4 h-4" />
            </a>
          </li>
        </ul>
        <select class="w-20 input box mt-3 sm:mt-0">
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide } from "vue";
import { Files, videosMT } from "./tutorial";

export default defineComponent({
  components: {},
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    let videos: Array<Files> = [];

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };
    const setVideos = () => {
      videos = videosMT();
    };
    const setTLS = (data: Files) => {
      window.localStorage.setItem(
        `${data.type}-${data.slug}`,
        JSON.stringify(data)
      );
    };

    setVideos();

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      videos,
      setTLS,
    };
  },
});
</script>
