
import { defineComponent, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  components: {},
  setup() {
    const config = api.configGet();

    const dataMP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 12,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingMP").show();

      const page = dataMP.page;
      const pageSize = dataMP.pageSize;
      const search = dataMP.search;
      axios
        .get(
          api.memberMateri + 'pdf/' +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          config
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          console.log("response get mp ", rs);

          dataMP.data = rs;
          dataMP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataMP.paginate ", dataMP.paginate);
          cash("#loadingMP").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingMP").hide();
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataMP.page = p;
      getData();
    };

    const nextPage = () => {
      dataMP.page = dataMP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataMP.page = dataMP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataMP.page = dataMP.paginate.totalPages;
      getData();
    };

    return {
      getData,
      dataMP,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      config,
    };
  },
});
