
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { helper as $h} from "@/utils/helper";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();

    const titleTable: Array<string> = [
      "Title",
      "Content",
    ];

    const dataMK = reactive({
      tempData: [] as any,
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
        dataPaginate: [],
      } as any,
    });

    const getDataPagination = async () => {
      const page = dataMK.page;
      const pageSize = dataMK.pageSize;
      dataMK.paginate = $h.paginationLocal(dataMK.tempData.length, page, pageSize, 3, dataMK.tempData);
      dataMK.data = dataMK.paginate.dataPaginate;
    };

    const getData = async () => {
      cash("#loadingDataMK").show();

      axios
        .get(
          api.salesMarketingKit, configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataMK.tempData = rs;
          await getDataPagination();
          // console.log("dataMK.paginate ", dataMK.paginate);
          cash("#loadingDataMK").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDataMK").hide();
          // console.log("response get du err ", error.response);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getDataPagination();
    };

    const goToPage = (p: number) => {
      dataMK.page = p;
      getDataPagination();
    };

    const nextPage = () => {
      dataMK.page = dataMK.page + 1;
      getDataPagination();
    };

    const beforePage = () => {
      dataMK.page = dataMK.page - 1;
      getDataPagination();
    };

    const lastPage = () => {
      dataMK.page = dataMK.paginate.totalPages;
      getDataPagination();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataMK,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
    };
  },
  data() {
    return {};
  },
  methods: {
  },
});
