type Files = {
  fileName: string;
  slug?: string | "not-found";
};

const dataPPPK: Array<Files> = [
  { fileName: "Tryout SKD", slug: "tryout-skd" },
  { fileName: "Tryout SKB", slug: "tryout-skb" },
];

const filesPPPK = (): Array<Files> => {
  return dataPPPK.map((e) => {
    const obj = {
      fileName: `${e.fileName}`,
      slug: `${e.slug}`,
    };
    return obj;
  });
};

export { Files, filesPPPK };
