<template>
  <div>
    <div
      class="flex justify-center md:justify-between flex-col sm:flex-row items-center mt-5"
    >
      <h2 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
        Hasil Tryout "{{ dataTH.title }}"
      </h2>
    </div>

    <div class="intro-y grid grid-cols-12 gap-3 sm:gap-2 mt-5">
      <div class="intro-y col-span-12 md:col-span-6 xxl:col-span-4 ">
        <div
          class="flex flex-row box items-center justify-between pt-4 sm:pt-5 px-3 sm:px-5 pb-4 border-b-2 border-gray-200 dark:border-dark-5"
        >
          <h3 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
            Data User
          </h3>
          <a
            id="vDtUser"
            @click="rnTop('vDtUser', 'dtUser')"
            href="javascript:;"
            class="font-bold cursor-pointer transform rotate-180"
          >
            <ChevronDownIcon class="w-6 h-6 mr-0" />
          </a>
        </div>
        <div id="dtUser" class="overflow-x-auto box">
          <table class="table">
            <tbody>
              <tr v-for="(du, idx) in dataTH.hasil" v-bind:key="idx">
                <td class="border-b whitespace-nowrap font-bold px-0">
                  {{ du.label }}
                </td>
                <td class="border-b whitespace-nowrap w-8 px-0">:</td>
                <td class="border-b whitespace-nowrap px-0">{{ du.value }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="intro-y col-span-12 md:col-span-6 xxl:col-span-4 ">
        <div
          class="flex flex-row box items-center justify-between pt-4 sm:pt-5 px-3 sm:px-5 pb-4 border-b-2 border-gray-200 dark:border-dark-5"
        >
          <h3 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
            Keterangan Ujian
          </h3>
          <a
            id="vKetUji"
            @click="rnTop('vKetUji', 'ketUji')"
            href="javascript:;"
            class="font-bold cursor-pointer transform rotate-180"
          >
            <ChevronDownIcon class="w-6 h-6 mr-0" />
          </a>
        </div>
        <div id="ketUji" class="overflow-x-auto box">
          <table class="table">
            <tbody>
              <tr v-for="(du, idx) in dataTH.keterangan" v-bind:key="idx">
                <td class="border-b font-bold px-0">{{ du.label }}</td>
                <td class="border-b w-8 px-0">:</td>
                <td class="border-b whitespace-nowrap px-0">{{ du.value }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div
        v-for="(als, idx) in dataTH.analisa"
        :key="idx"
        class="intro-y col-span-12 md:col-span-6 xxl:col-span-4 "
      >
        <div
          class="flex flex-row box items-center justify-between pt-4 sm:pt-5 px-3 sm:px-5 pb-4 border-b-2 border-gray-200 dark:border-dark-5"
        >
          <h3 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
            {{ als.label }}
          </h3>
          <a
            :id="'vAls'+idx"
            @click="rnTop('vAls'+idx, 'ketAls'+idx)"
            href="javascript:;"
            class="font-bold cursor-pointer"
          >
            <ChevronDownIcon class="w-6 h-6 mr-0" />
          </a>
        </div>
        <div :id="'ketAls'+idx" class="overflow-x-auto box hidden">
          <table class="table">
            <tbody>
              <tr>
                <td class="border-b font-bold px-0">Poin</td>
                <td class="border-b w-8 px-0">:</td>
                <td class="border-b whitespace-nowrap px-0">{{ als.poin }}</td>
              </tr>
              <tr>
                <td class="border-b font-bold px-0">Passing Grade</td>
                <td class="border-b w-8 px-0">:</td>
                <td class="border-b whitespace-nowrap px-0">{{ als.passingGrade }}</td>
              </tr>
              <tr v-for="(du, idx) in als.items" v-bind:key="idx">
                <td class="border-b font-bold px-0">{{ du.label }}</td>
                <td class="border-b w-8 px-0">:</td>
                <td class="border-b whitespace-nowrap px-0">{{ du.percent }}</td>
              </tr>
            </tbody>
          </table>
          <p class="px-5 py-3 font-bold">Notes : {{ als.notes }}</p>
        </div>
      </div>
    </div>

    <!-- BEGIN: Wizard Layout -->
    <div class="intro-y grid grid-cols-12 gap-3 sm:gap-2 mt-3 md:mt2">
      <!-- BEGIN Pembasan Soal -->
      <div class="intro-y col-span-12 box px-3 sm:px-5 pb-5">
        <div
          class="flex flex-row items-center justify-between py-5 border-b border-gray-200 dark:border-dark-5"
        >
          <h3 class="intro-y text-lg font-medium m-0 md:mr-auto md:ml-0">
            Pembahasan Soal
          </h3>
          <div
            class="grid grid-cols-12 w-24 sm:w-32 button p-0 items-center justify-center text-white cursor-pointer"
          >
            <span
              class="inline-block col-span-5 sm:col-span-4 py-3 px-1 bg-blue-800 text-center rounded rounded-r-none"
              >NO</span
            >
            <span
              class="inline-block col-span-7 sm:col-span-8 p-3 bg-blue-600 text-center rounded rounded-l-none"
              >{{ cis + 1 }}</span
            >
          </div>
        </div>

        <div class="pt-3 pb-2">
          <h3 class="text-base font-medium mt-1 mb-1 uppercase">
            {{ cs.kategori }}
          </h3>
          <img v-if="cs.questionImg" :src="cs.questionImg" class="m-2" alt="Image Soal">
          <p
            class="border-b border-gray-500 dark:border-gray-600 p-2 mb-2 bg-gray-200 dark:bg-dark-1"
            v-html="cs.question"
          ></p>
          <ol>
            <li
              v-for="(opt, idx) in cs.options"
              v-bind:key="idx"
              :opt="'chs-' + cs.nomor + '-' + idx"
              :class="[
                'flex flex-row justify-items-center my-3 md:my2 button p-0 list-chs',
                cs.answerUser === opt.option ? 'active' : '',
                opt.text ? '' : 'hidden',
              ]"
            >
              <span
                class="inline-block px-2 py-2 w-8 rounded rounded-r-none text-center border border-gray-500 dark:border-gray-600"
                >{{ opt.option }}
              </span>
              <span
                class="inline-block pl-2 sm:pl-3 pr-2 py-2 w-full rounded rounded-l-none text-left border border-gray-500 dark:border-gray-600"
              >
                <img v-if="opt.image" :src="opt.image" alt="Image Opsi Soal">
                <br v-if="opt.image" />
                {{ opt.text }}
              </span>
            </li>
          </ol>
        </div>

        <div
          class="items-center justify-center justify-items-center pt-3 pb-5 border-t-2 border-green-500 dark:border-green-800"
        >
          <h3 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
            Pembahasan
          </h3>
          <p
            class="border-b border-gray-500 dark:border-gray-600 p-2 bg-gray-200 dark:bg-dark-1"
            v-html="trimText(cs.pembahasan)"
          ></p>
        </div>

        <div
          class="items-center justify-center justify-items-center pt-3 pb-5 border-t-2 border-green-500 dark:border-green-800"
        >
          <h3 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
            Kunci Jawaban
          </h3>
          <ol>
            <li
              v-for="(opt, idx) in cs.options"
              v-bind:key="idx"
              :opt="'chs-' + cs.nomor + '-' + idx"
              :class="[
                'flex flex-row justify-items-center my-3 md:my2 button p-0 list-chs',
                cs.answer === opt.option ? 'active' : 'hidden',
              ]"
            >
              <span
                class="inline-block px-2 py-2 w-8 rounded rounded-r-none text-center border border-gray-500 dark:border-gray-600"
                >{{ opt.option }}
              </span>
              <span
                class="inline-block pl-2 sm:pl-3 pr-2 py-2 w-full rounded rounded-l-none text-left border border-gray-500 dark:border-gray-600"
                >{{ opt.text }}
              </span>
            </li>
          </ol>
          <p class="font-bold text-primary">
            Point Anda {{ trimText(cs.poinUser) }}
          </p>
        </div>

        <div
          class="flex flex-row items-center justify-center justify-items-center pt-5 border-t border-gray-200 dark:border-dark-5"
        >
          <button
            v-if="cis !== 0"
            class="button w-auto h-11 mr-2 md:mr-5 pl-1 md:pr-5 flex items-center justify-center bg-theme-1 text-white"
            @click="changeSoalCis('back')"
          >
            <ChevronLeftIcon class="w-6 h-6 ml-1 mr-0 sm:mr-2 font-bold" />
            <span class="hidden sm:block">Back</span>
          </button>
          <button
            class="button w-auto h-11 ml-2 md:ml-5 pl-3 md:pl-5 md:pr-1 pr-2 sm:pr-1 flex items-center justify-center bg-theme-1 text-white flex-row-reverse"
            @click="changeSoalCis('next')"
          >
            <ChevronRightIcon class="w-6 h-6 ml-0 sm:ml-2 font-bold mr-auto" />
            <span class="hidden sm:block">Next</span>
          </button>
        </div>
      </div>
      <!-- END Pembahasan Soal -->

      <!-- BEGIN Panel Soal with Number -->
      <div class="intro-y col-span-12 box p-2">
        <div
          class="intro-y col-span-12 flex flex-row justify-between border-b border-gray-200 dark:border-dark-5 pt-2 pb-3"
        >
          <h3 class="intro-y text-lg font-medium w-full">Navigasi Soal</h3>
          <a
            id="cvRn"
            @click="rn()"
            href="javascript:;"
            class="font-bold cursor-pointer"
          >
            <ChevronDownIcon class="w-6 h-6 mr-0" />
          </a>
        </div>
        <div class="w-auto overflow-auto scrollbar-nav-soal mb-3">
          <ol id="nsRn" class="flex flex-row">
            <li
              v-for="(soal, idx) in allSoal"
              v-bind:key="idx"
              :class="[
                'my-2 px-2 list-ns',
                soal.answerUser === trimText(soal.answer) ? 'yakin' : '',
                soal.answerUser !== trimText(soal.answer) ? 'salah' : '',
                idx === cis ? 'active' : '',
              ]"
            >
              <button
                class="intro-y w-16 h-10 rounded button bg-gray-200 dark:bg-dark-1 text-gray-600 px-1"
                @click="changeSoal(soal, idx)"
              >
                {{ idx + 1 }} - {{ soal.answerUser === undefined ? "" : soal.answerUser }}
                <span
                  v-if="idx === cis"
                  class="inline-block absolute px-1 top-0 right-0 bg-primary rounded-bl-lg rounded-tr-sm"
                  style="font-size: 10px"
                >N</span>
              </button>
            </li>
          </ol>
        </div>
      </div>
      <!-- END Panes Soal with Number -->

    </div>
    <!-- END: Wizard Layout -->
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { useRoute } from "vue-router";
import { resultExam, thDataUser, thKeterangan } from "./tryout-hasil";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const route = useRoute();
    const params = route.params;
    const resp = resultExam.response;
    const soalList = resultExam.response.soal;
    const config = api.configGet();
    const kodeExam = gpf.getParameterByName("kode");
    const jwbCat: any = JSON.parse(gpf.memberHasilCat(kodeExam));

    const rn = (): void => {
      cash("#cvRn").toggleClass("transform rotate-180");
      cash("#nsRn").toggleClass("flex-wrap justify-center");
    };

    const rnTop = (id1: string, id2: string): void => {
      cash("#" + id1).toggleClass("transform rotate-180");
      cash("#" + id2).toggleClass("hidden");
    };

    return {
      params,
      rn,
      resp,
      soalList,
      rnTop,
      kodeExam,
      jwbCat,
      config,
    };
  },
  data() {
    return {
      cs: {
        id: 0,
        nomor: 0,
        kategori: "",
        bidang: "",
        question: "",
        questionImg: "",
        option: [
          {
            option: "",
            text: "",
            img: "",
          },
        ],
      },
      allSoal: resultExam.response.soal,
      cis: 0,
      dataUser: thDataUser,
      ketUjian: thKeterangan,
      dataTH: {},
    };
  },
  methods: {
    changeSoal(soal: any, idx: number): void {
      this.cs = soal;
      this.cis = idx;
    },
    changeSoalCis(tp: string): void {
      tp === "next" ? this.cis++ : this.cis--;
      this.cs = this.allSoal[this.cis];
    },
    chooseOpt(cs: any, no: any, idx: any): void {
      const a = cs.option;
      for (let i = 0; i < a.length; i++) {
        if (i !== idx) {
          cash('[opt="chs-' + no + "-" + i + '"]').removeClass("active");
        } else {
          cash('[opt="chs-' + no + "-" + idx + '"]').addClass("active");
          this.allSoal[this.cis].answer = cs.option[idx].option;
          this.allSoal[this.cis].answerIdx = idx;
        }
      }
    },
    trimText(txt: string): string {
      return gpf.trimText(txt);
    },
    async memberHasilCat() {
      axios
        .get(api.memberHasilCat + this.kodeExam, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get sc ", rs);
          this.dataTH = rs;

          window.localStorage.setItem(
            "submitCatApi" + this.kodeExam,
            JSON.stringify(rs)
          );

          await this.memberPembahasanCat();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();

          // console.log("response get sc err ", error.response);
          await gpf.handleEds(error);
        });
    },
    async memberPembahasanCat() {

      axios
        .get(api.memberPembahasanCat + this.kodeExam, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get spc ", rs);
          this.allSoal = rs;
          this.cs = this.allSoal[this.cis];
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();

          await gpf.handleEds(error);
        });
    },
  },
  mounted() {
    gpf.gLoading.show();

    setTimeout(() => {
      this.memberHasilCat();
    }, 1500);

  },
});
</script>

<style lang="scss" scoped>
.list-chs {
  &.active {
    > span {
      &:first-child {
        @apply bg-green-700 dark:bg-green-800 text-white;
      }
      &:last-child {
        @apply bg-green-500 dark:bg-green-600 text-white;
      }
    }
  }
}
.list-ns {
  &.active {
    > button {
      @apply bg-primary text-white;
    }
  }
  &.ragu {
    > button {
      @apply bg-yellow-300 text-white;
    }
  }
  &.yakin {
    > button {
      @apply bg-green-500 dark:bg-green-700 text-white;
    }
  }
  &.salah {
    > button {
      @apply bg-red-500 dark:bg-red-700 text-white;
    }
  }
}
</style>
